        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      // 修改 => 获取数据
      getOldData (id, url) {
        console.log('—————————————————————————————————从后台获取———————————————————————————————')
        console.log('接收到的参数：' + id + ', ' + url)
        const self = this
        axios.post(url, {id: id}, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}}
        ).then(function (res) {
          console.log('接收到的信息如下：')
          self.jdPictures = res.data.pices.split(';')
          if (self.GetQueryString('type') === 'sight') {
            self.dataQianTai.name = res.data.name
            self.dataQianTai.price = res.data.price
            self.dataQianTai.fengmian = 'img/scienspot/cover/' + res.data.cover
            self.dataQianTai.phone = res.data.cinformation
            self.dataQianTai.jianjie = res.data.synosis
            self.dataQianTai.video = 'img/scienspot/videos/' + res.data.video
            self.jdPictures.forEach(function (value, index) {
              self.jdPictures[index] = 'img/scienspot/otherPic/' + value
            })
          }
          else if (self.GetQueryString('type') === 'canguan') {
            self.dataQianTai.name = res.data.name
            self.dataQianTai.price = res.data.perprice
            self.dataQianTai.fengmian = 'img/restaurant/cover/' + res.data.cover
            self.dataQianTai.phone = res.data.phone
            self.dataQianTai.jianjie = res.data.introduce
            self.dataQianTai.video = 'img/restaurant/videos/' +  + res.data.video
            self.jdPictures.forEach(function (value, index) {
              self.jdPictures[index] = 'img/restaurant/otherPic/' + value
            })
          }
          else if (self.GetQueryString('type') === 'hotle') {
            self.dataQianTai.name = res.data.hotelname
            self.dataQianTai.price = res.data.lowprice
            self.dataQianTai.fengmian = 'img/hotel/cover/' + res.data.mainpic
            self.dataQianTai.phone = res.data.phone
            self.dataQianTai.jianjie = res.data.introduce
            self.dataQianTai.video = 'img/hotel/videos/' +  + res.data.video
            self.jdPictures.forEach(function (value, index) {
              self.jdPictures[index] = 'img/hotel/otherPic/' + value
            })
          }
          else {
            console.log('错误：url的type属性')
            return
          }
          // 需要处理 分解成隶属xian 和隶属xiang
          self.dataQianTai.location = res.data.location
          var arrayLocation = res.data.location.split(' ')
          self.lishuxian = arrayLocation[3]
          self.lishuxiang = arrayLocation[4]
          self.dataQianTai.adress = res.data.address
          console.log('获取到的数据如下：')
          console.log('>名字：')
          console.log(self.dataQianTai.name)
          console.log('>简介：')
          console.log(self.dataQianTai.jianjie)
          console.log('>隶属：')
          console.log(self.lishuxian + ' ' + self.lishuxiang)
          console.log('>图片集：')
          console.log(self.jdPictures)
          console.log('>联系方式：')
          console.log(self.dataQianTai.phone)
          console.log('>详细地址：')
          console.log(self.dataQianTai.adress)
          console.log('>价格：')
          console.log(self.dataQianTai.price)
          console.log('>封面：')
          console.log(self.dataQianTai.fengmian)
          console.log('>视频：')
          console.log(self.dataQianTai.video)
        }
        ).catch(function (err) {
          console.log('s获取修改数据，连接错误~')
          /*
