            "userid":1,
            "countryId":1,
            "username":"阿水12344",
            "synosis":"简介",
            "price":"20"
          }
          */
        })
      },
      /* text模态框1 */
      closeTextModal () {
        this.showModalValue = ''
      },
      /* text模态框1 */
      alertTextModal (index) {
        this.showModalValue = 'modal-text'
        this.textModalData = this.textModalDatas[index]
      },
      /* 尚未开启的功能 */
      sorry () {
        this.alertModal('您真细心发现了它,但是该功能还有待开发')
      }
    }
  }
</script>

