<template>
  <div id="app">
    <div class="nav">
      <div class="logo">
        <router-link class="aFont" :to="{ path: '/' }">
          <img src="static/logo1.png" alt="">
        </router-link>
      </div>
      <div class="navLinkUl">
        <ul>
          <li>
            <router-link class="aFont" :to="{ path: '/show/countrysides' }">
              所有乡村
            </router-link>
          </li>
          <li>
            <router-link class="aFont" :to="{ path: '/show/hello' }">
              路线
            </router-link>
          </li><!--
          <li>
            <router-link class="aFont" :to="{ path: '/show/index' }">
              测试
            </router-link>
          </li>-->
        </ul>
      </div>
      <div id="headp" class="headp" v-show="showuser">
        <img :src="person.mainpic" @mouseout="showoption=false" @mouseover="showoption=true">
        <ul v-show="showoption"  @mouseout="showoption=false" @mouseover="showoption=true">
          <li>
            <a :href="centerUrl" class="aFontColor">
              {{person.name}}
            </a>
          </li>
          <li @click="sendExit">退出登录</li>
        </ul>
      </div>
      <div id="loadAndRe" class="loadAndRe" v-show="!showuser">
        <ul>
          <li><router-link :to="{ path: '/loadandreg/load' }" class="aFont">登录</router-link></li>
          <li><router-link :to="{ path: '/loadandreg/register' }" class="aFont">注册</router-link></li>
        </ul>
      </div>
    </div>
    <router-view></router-view>
    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
    <!-- confirm -->
    <div class="modal" v-if="showModalValue === 'modal-confirm'" style="z-index: 2000">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close" style="width: 50%;float: left;">
            <a href="javascript:;" class="link-btn" @click="closeBtn('quxiao')" style="color: #cccccc">取消</a>
          </button>
          <button class="alter-btn-close" style="width: 50%;float: left">
            <a href="javascript:;" class="link-btn" @click="closeBtn('queren')">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'app',
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        /* ——————————————————————————————————confirm模态框—————————————————————————————————— */
        confirmRes: '',
        /* ————————————切换数据—————— */
        // 用户 浮动窗口
        showoption: false,
        // 用户状态
        showuser: false,
        /* ————————————数据—————— */
        // 用户信息
        person: {
          name: '',
          mainpic: '',
          type: ''
        },
        // 用户个人中心的url
        centerUrl: '',
        cheshi: 0,
        // 判断需要执行普通用户吗
        doAxios2: true
      }
    },
    methods: {
      /* alert模态框1 */
      closeBtn (parm) {
        if (this.showModalValue === 'modal-confirm') {
          if ( parm === 'quxiao') {
            this.confirmRes = 'quxiao'
            this.sendExit()
          } else {
            this.confirmRes = 'queren'
            this.sendExit()
          }
        }
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;'
        this.showModalValue = 'modal-alter'
      },
      /* confirm模态框2 */
      confirmModal (alertText) {
        this.noticeText = alertText
        this.showModalValue = 'modal-confirm'
      },
      /* 退出当前用户 */
      sendExit () {
        console.log(this.confirmRes)
        if (this.confirmRes === ''){
          this.confirmModal('确认退出吗？')
        } else if (this.confirmRes === 'queren') {
          var url = ''
          if (this.person.type === 'putong') {
            url = 'user/exit'
          } else {
            url = 'CountrysideUser/exit'
          }
          const self = this
          axios.post(url, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }).then(function (res) {
            console.log('退出成功,res:')
            console.log(res)
            self.showuser = false
            self.showoption = false
            self.person.name = ''
            self.person.mainpic = ''
            self.person.type = ''
            self.centerUrl = ''
            self.confirmRes = ''
            window.location.href = '/ssm01#/'
          }).catch(function (err) {
            console.log('退出失败' + err)
            self.alertModal('退出失败')
            self.confirmRes = ''
          })
        } else if (this.confirmRes === 'quxiao') {
          /*if (!this.confirmRes) {
            self.alertModal('取消退出')
            self.confirmRes = ''
          */
          this.confirmRes = ''
        }
      },
      /* 直接退出当前用户 */
      sendExit2 () {
        var url = ''
        if (this.person.type === 'putong') {
          url = 'user/exit'
        } else {
          url = 'CountrysideUser/exit'
        }
        const self = this
        axios.post(url, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }).then(function (res) {
          console.log('默认退出成功,res:')
          console.log(res)
          self.showuser = false
          self.showoption = false
          self.person.name = ''
          self.person.mainpic = ''
          self.person.type = ''
          self.centerUrl = ''
        }).catch(function (err) {
          console.log('默认退出失败' + err)
        })
      },
      /* 用户状态 */
      getUser () {
        const self = this
        /* 1、判断商业用户是否登入 */
        axios.post('CountrysideUser/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }
        ).then(function (res) {
          if (res.data === '未登录') {
            console.log('商业用户没有登录')
          }
          else {
            // 商业用户已登录
            self.doAxios2 = false
            self.showuser = true
            axios.post('CountrysideUser/info', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
            }
            ).then(function (res) {
              console.log('商业用户，信息：')
              console.log(res)
              self.person.name = res.data.countrysideUser[0].name
              self.person.mainpic = 'img/headpic/' + res.data.countrysideUser[0].headpic
              self.person.type = 'shangye'
              self.centerUrl = '#/person/xiangcun?tab=xiangcun'
            }
            ).catch(function (err) {
              console.log('商业信息获取失败：' + err)
            })
          }
        }
        ).catch(function (err) {
          console.log('商业用户判断404：' + err)
        })
        if (this.doAxios2) {
          // 判断普通用户是否登入 （商业用户未登录）
          axios.post('user/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }
          ).then(function (res) {
            if (res.data === '未登录') {
              console.log('普通用户也没有登录')
            }
            else {
              // 普通用户已登录
              self.showuser = true
              axios.post('user/info', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}}
              ).then(function (res) {
                console.log('普通用户，信息：')
                console.log(res)
                self.person.name = res.data.user[0].username
                self.person.mainpic = 'img/headpic/' + res.data.user[0].headpic
                self.person.type = 'putong'
                self.centerUrl = '#/personordinary'
              }
              ).catch(function (err) {
                console.log('普通信息获取失败：' + err)
              })
            }
          }
          ).catch(function (err) {
            console.log('普通用户判断404：' + err)
          })
        }
      },
      /* 监听是否获取用户状态 */
      getUserListener () {
        if (this.person.type !== '') {
          if (window.location.href.indexOf('loadandreg/register') >= 0 || window.location.href.indexOf('loadandreg/load') >= 0) {
            this.sendExit2()
            console.log('sendExit2')
          }
        }
        var parmUser = this.GetQueryString('get')
        if (!parmUser) {
          // 没有登录过
          return
        } else {
          if (!this.showuser) {
            // 还没有用户
            this.getUser()
          }
          else {
            // 有用户了 退出
            return
          }
        }
      },
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      }
    },
    created () {
      this.getUser ()
    },
    mounted () {
      window.addEventListener('mouseover', this.getUserListener)
    }
  }
</script>

<style scoped>
  @import './styles/main.css';
  @import './styles/model.css';
  .aFontColor{
    color: #fff;
    text-decoration: none;
  }
  .nav{
    height: 80px;
    width: 1000px;
    margin: 0 auto;
    display: flex;
    align-items: center;
  }
  .logo{
    width: 200px;
    height: 80px;
    float: left;
  }
  .logo a{
    display: block;
    width: 200px;
    height: 80px;
    display: flex;
    align-items: center;
  }
  .logo a img{
    width: 200px;
    height: auto;
  }
  .loadAndRe{
    float: right;
    height: 80px;
    width: 130px;
    line-height: 80px;
    display: flex;
    align-items: center;
    margin-left: 330px;
  }
  .loadAndRe li{
    height: 20px;
    line-height: 20px;
    width: 60px;
    font-size: 16px;
    text-align: center;
    float: left;
    cursor: pointer;
  }
  .loadAndRe li:nth-child(1){
    border-right: 2px solid #c3c3c3;
  }
  .headp{
    height: 80px;
    width: 130px;
    float: right;
    line-height: 80px;
    /*margin: 5px 5px;*/
    border-radius: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 400px;
  }
  .headp img{
    width: 50px;
    height: 50px;
    cursor: pointer;
    border-radius: 50%;
  }
  .headp ul{
    position: absolute;
    width: 110px;
    top: 50px;
    left: 0px;
    background: rgba(0,2,0,0.9);
    z-index: 20;
  }
  .headp ul li{
    height: 40px;
    color: #fff;
    line-height: 40px;
    padding-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-top: 1px solid #000;
    cursor: pointer;
  }
  .headp ul li:hover{
    background: #292929;
  }
  .headp ul li a{
    display: block;
    width: 100%;
    height: 100%;
  }
  .navLinkUl{
    float: left;
    height: 80px;
    width: 300px;
    line-height: 80px;
    margin: 0px 0px 0px 20px;
    display: flex;
    align-items: center;
  }
  .navLinkUl ul li{
    padding: 0px 12px;
    height: 40px;
    line-height: 40px;
    float: left;
  }
  .navLinkUl ul li a{
    font-size: 18px;
    color: #cecece;
  }
  .navLinkUl ul li a:hover{
    color: #000;
  }

</style>



