<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active">我的餐馆</span>
    </div>
    <div class="midr2 content">
      <div v-if="myCanguan.length === 0">
        <router-link :to="{path: '/add/jingdianAdd', query:{type: 'canguan'}}" target="_blank">
          <div class="divNone">
            <img src="static/moren.jpg" alt="">您还没有添加餐馆哟，快点击添加吧~
          </div>
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div v-if="myCanguan.length > 0">
        <router-link class="btn btn1" :to="{path: '/add/jingdianAdd', query:{type: 'canguan'}}" target="_blank">
          添加餐馆
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myCg, index) in myCanguan">
        <div style="width: 300px;float: left;" class="imgcenter">
          <img :src="myCg.cgImg">
        </div>
        <div class="cellr">
          <router-link :to="{path: '/displayremark/sight', query: { id: myCg.cgId , type: 'canguan'}}" target="_blank">
            <h4 v-text="myCg.cgName"></h4>
          </router-link>
          <p class="">{{myCg.location}}</p>
          <p class="" v-text="myCg.cgTime"></p>
          <p>
            <router-link class="btn btn-sm" :to="{path: '/add/jingdianAdd', query: { id: myCg.cgId , type: 'canguan', type2: 'change'}}" target="_blank">
              修改
            </router-link>
            <a href="javascript:;" class="btn btn-sm btn-gray" @click="deleteSandian(myCg.cgId)" style="margin-left: 20px">
              删除
            </a>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
    <!-- confirm -->
    <div class="modal" v-if="showModalValue === 'modal-confirm'" style="z-index: 2000">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close" style="width: 50%;float: left;">
            <a href="javascript:;" class="link-btn" @click="closeBtn('quxiao')" style="color: #cccccc">取消</a>
          </button>
          <button class="alter-btn-close" style="width: 50%;float: left">
            <a href="javascript:;" class="link-btn" @click="closeBtn('queren')">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        /* ——————————————————————————————————confirm模态框—————————————————————————————————— */
        confirmRes: '',
        myCanguan: [],
        cellmyCanguan: {}
      }
    },
    created () {
      this.getData()
    },
    methods: {
      /* 删除 */
      deleteSandian (id) {
        if (this.confirmRes === ''){
          this.confirmModal('真的要删除吗？')
        } else if (this.confirmRes === 'queren') {
          const self = this
          axios.post('deleteSRH/delSRH', {id: id, type: 'restaurant'},{headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }).then(function (res) {
            console.log('删除成功' + res)
            window.location.reload()
          }).catch(function (err) {
            console.log('删除失败' + err)
            self.alertModal('删除失败')
            self.confirmRes = ''
          })
        } else if (this.confirmRes === 'quxiao') {
          this.confirmRes = ''
        }
      },
      /* alert模态框1 */
      closeBtn (parm) {
        if (this.showModalValue === 'modal-confirm') {
          if ( parm === 'quxiao') {
            this.confirmRes = 'quxiao'
            this.deleteSandian()
          } else {
            this.confirmRes = 'queren'
            this.deleteSandian()
          }
        }
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;'
        this.showModalValue = 'modal-alter'
      },
      /* confirm模态框2 */
      confirmModal (alertText) {
        this.noticeText = alertText
        this.showModalValue = 'modal-confirm'
      },
      getData () {
        const self = this
        axios.get('restaurant/allRestaurants').then(function (res) {
          console.log('获取个人餐馆成功！')
          self.myCanguan = []
          var data2_cg = res.data.restaurants
          for (var datacgI = 0; datacgI < data2_cg.length; datacgI++) {
            self.cellmyCanguan = {}
            self.cellmyCanguan.cgName = data2_cg[datacgI].name
            self.cellmyCanguan.cgImg = 'img/restaurant/cover/' + data2_cg[datacgI].cover
            self.cellmyCanguan.cgId = data2_cg[datacgI].restaurantid
            self.cellmyCanguan.cgTime = data2_cg[datacgI].addtime
            self.cellmyCanguan.location = data2_cg[datacgI].location
            self.myCanguan[datacgI] = self.cellmyCanguan
          }
        }).catch(function (err) {
          console.log('获取个人餐馆失败：' + err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



