<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        show: 'youji',
        /* ——————————————————————————————————其他—————————————————————————————————— */
        // 报错
        baocuo: [
          {
            time: '2017/3/9',
            name: 'http://localhost:63342/mlxc/center_tz.html##',
            state: '通过',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: 'http://localhost:63342/mlxc/center_tz.html##',
            state: '未通过',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: 'http://localhost:63342/mlxc/center_tz.html##',
            state: '审核中',
            id: '1'
          }
        ],
        /* ——————————————————————————————————前后台—————————————————————————————————— */
        // 我的游记
        youji: [],
        // 我的收藏
        shoucang: [],
        // 新建游记
        yjAdd: {
          title: '',
          lishuxian: '递铺镇',
          lishuxiang: '',
          fengmian: '',
          zhengwen: ''
        },
        // 乡村
        xiangcun: [
          "余村村",
          "双一村",
          "横山坞村",
          "刘家塘村",
          "马家弄村",
          "高家堂村",
          "尚书干村",
          "长林垓村",
          "九庙村"
        ],
        /* ——————————————————————————————————前台—————————————————————————————————— */
        // 个人资料 后台接收（用于显示）
        userInfo: {
          userName: '阿水12344',
          signature: '这位同学很懒，木有签名的说～',
          userPic: 'static/moren.jpg',
          dengji: 1,
          fensi: 2,
          guanzu: 3,
          jingyan: 0,
          sex: 1,
          birthday: '2017-05-20',
          city: '上海',
          phone: 'aaa',
          wechat: 'aaa'
        },
        // 个人资料 修改页面
        userInfo2: {
          username: '无名',
          sex: '0',
          birth: '2017-01-01',
          city: '杭州',
          signature: '无',
          headPic: 'static/moren.jpg',
          phone: '1782680****',
          wechat: 'wm1782680****',
          level: '0',
          fans: '0',
          attention: '0',
          experience: '0'
        },
        /* ——————————————————————————————————后台—————————————————————————————————— */
        // 新建游记 sendData
        sendData: new window.FormData(),
        // 个人资料 表单格式 param
        param: new window.FormData(),
        // 密码
        password: {
          oldpwd: '',
          newpwd: ''
        },
        content: ''
      }
    },
    methods: {
      /* alert模态框1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
            this.showModalValue = 'modal-alter'
      },
      /* tab选项卡 */
      showWho (str) {
        this.show = str
      },
      /* 通知的样式（红绿灰）判断 */
      showStyle1 (state, event, type, obj) {
        if (state === '通过') {
          if (event === 'color') {
            return 'green'
          } else if (event === 'chakanSrc') {
            if (type === '乡村') {
              return '/show/xc?id='
            } else if (type === '景点') {
              return '/show/jd'
            } else if (type === '餐馆') {
              return '/show/cg'
            } else if (type === '住宿') {
              return '/show/zs'
            }
          } else if (event === 'chakanText') {
            return '查看页面'
          } else if (event === 'xiugaiText') {
            return ' '
          }
        } else if (state === '未通过') {
          if (event === 'color') {
            return 'red'
          } else if (event === 'chakanSrc') {
            return 'javascript:;'
            /* 触发模态框 */
          } else if (event === 'chakanText') {
            return '查看原因'
          } else if (event === 'xiugaiText') {
            return '修改重交'
          }
        } else {
          if (event === 'color') {
            return 'gray'
          } else if (event === 'chakanSrc') {
            return 'javascript:;'
            /* 触发模态框 */
          } else if (event === 'chakanText') {
            return '　'
          } else if (event === 'xiugaiText') {
            return '修改重交'
          }
        }
      },
      /* 新建游记 */
      sendYouji () {
        this.sendData.set('name', this.yjAdd.title)
        this.sendData.set('location', '浙江省湖州市安吉县' + this.yjAdd.lishuxian + this.yjAdd.lishuxiang)
        this.sendData.set('content', this.yjAdd.zhengwen)
        console.log('要提交的数据是： { 标题: ' + this.sendData.get('name') + ' ,隶属: ' + this.sendData.get('location') + ' ,封面: ' + this.sendData.get('mainPic') + ' ,正文： ' + this.sendData.get('content') + ' }')
        // 判断上传的数据是否为空
        if (!this.sendData.get('name')) {
          this.alertModal('标题不能为空')
          return
        } else if (!this.sendData.get('mainPic')) {
          this.alertModal('游记不能为空')
          return
        } else if (!this.sendData.get('content')) {
          this.alertModal('内容不能为空')
          return
        }
        const self = this
        axios.post('userarticle/newarticle2', self.sendData, {headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data === '发表成功') {
            self.alertModal('发布游记成功')
            self.getPersonYouji()
            /* window.location.reload() */
          } else {
            self.alertModal('发布游记失败')
          }
        }).catch(function (err) {
          console.log(err)
          self.alertModal('发布游记失败')
        })
      },
      /* 获取个人游记 引dateFormat */
      getPersonYouji () {
        const self = this
        axios.get('userarticle/UserArticles').then(function (res) {
          self.youji = []
          self.youji = res.data.articles
          for (var i = 0; i < self.youji.length; i++) {
            self.youji[i].articletime = self.dateFormat(self.youji[i].articletime)
          }
          console.log('第二获取个人游记成功！')
        }).catch(function (err) {
          console.log('第二获取个人游记失败：' + err)
        })
      },
      /* 获取个人收藏 */
      getPersonShoucang () {
        const self = this
        axios.get('CountrysideColl/allColls').then(function (res) {
          self.shoucang = []
          self.shoucang = res.data.CountrysideColls
          for (var i = 0; i < self.shoucang.length; i++) {
            self.shoucang[i].colltime = self.dateFormat(self.shoucang[i].colltime)
            self.shoucang[i].mainpic = 'img/countryside/mainPic/' + self.shoucang[i].mainpic
          }
          console.log('获取个人收藏成功！')
        }).catch(function (err) {
          console.log('获取个人收藏失败：' + err)
        })
      },
      /* 获取个人资料 */
      getPersonInfo () {
        const self = this
        axios.get('user/info').then(function (res) {
          console.log('获取普通用户的数据：' + res)
          self.userInfo.userName = res.data.user[0].username
          self.userInfo.userPic = 'img/headpic/' + res.data.user[0].headpic
          if (res.data.user[0].sex === '男') {
            self.userInfo.sex = '0'
          } else {
            self.userInfo.sex = '1'
          }
          //
          if (res.data.user[0].birth !== undefined) {
            self.userInfo.birthday = self.dateFormat(res.data.user[0].birth)
          } else {
            self.userInfo.birthday = ''
          }
          self.userInfo.city = res.data.user[0].city
          self.userInfo.signature = res.data.user[0].signature
          self.userInfo.phone = res.data.user[0].phone
          self.userInfo.wechat = res.data.user[0].wechat
          self.userInfo.dengji = res.data.user[0].level
          self.userInfo.jingyan = res.data.user[0].experience
          self.userInfo.fensi = res.data.user[0].fans
          self.userInfo.guanzu = res.data.user[0].attention
          // 个人资料页面
          self.userInfo2.username = self.userInfo.userName
          self.userInfo2.sex = self.userInfo.sex
          self.userInfo2.city = self.userInfo.city
          self.userInfo2.headPic = self.userInfo.userPic
          self.userInfo2.birth = self.userInfo.birthday
          self.userInfo2.signature = self.userInfo.signature
          self.userInfo2.phone = self.userInfo.phone
          self.userInfo2.wechat = self.userInfo.wechat
        }).catch(function (err) {
          console.log('第二获取个人资料失败：' + err)
        })
      },
      /* 修改个人资料 */
      sendDataFun () {
        this.param.set('username', this.userInfo2.username)
        this.param.set('sex', this.userInfo2.sex)
        this.param.set('birth', this.userInfo2.birth)
        this.param.set('city', this.userInfo2.city)
        this.param.set('signature', this.userInfo2.signature)
        this.param.set('phone', this.userInfo2.phone)
        this.param.set('wechat', this.userInfo2.wechat)
        console.log('要上传的数据：{用户名：' + this.param.get('username') + ',性别： ' + this.param.get('sex') + ',生日： ' + this.param.get('birth') + ',城市： ' + this.param.get('city') + ',个性签名： ' + this.param.get('signature') + ',头像： ' + this.param.get('headpic') + ',手机： ' + this.param.get('phone') + ',微信： ' + this.param.get('wechat'))
        const self = this
        axios.post('user/changeUserInfo', self.param, {
          headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data.info === '修改成功') {
            self.alertModal('修改成功~')
            self.getPersonInfo()
            /* window.location.reload() */
          } else if (res.data.info === '用户名已存在') {
            self.alertModal('用户名已存在~')
            window.location.reload()
          } else if (res.data.info === '手机号已注册') {
            self.alertModal('手机号已注册~')
            window.location.reload()
          } else {
            self.alertModal('修改失败~')
            window.location.reload()
          }
        }).catch(function (err) {
          console.log(err)
          self.alertModal('修改失败~')
        })
      },
      /* 上传单张图片 */
      imageInputFun (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          if (/^((?!image).)+/g.test(choosePic.files[i].type)) {
            this.alertModal('请选择正确的图片格式！')
            i = 99999
          } else {
            showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
            var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
            imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
            if (class2 === 'xc_cover2') {
              this.userInfo2.userPic = imgObjPreview.src
              this.param.set('headpic', choosePic.files[0], choosePic.files[0].name)
            } else if (class2 === 'xc_cover') {
              this.yjAdd.fengmian = imgObjPreview.src
              this.sendData.set('mainPic', choosePic.files[0], choosePic.files[0].name)
            }
          }
        }
      },
      /* 修改密码 */
      sendPasswd () {
        if (this.passwd2 !== this.passwd3) {
          this.alertModal('两次输入的密码不相同！')
        } else if (!/^([a-zA-Z0-9]|[_]){6,20}$/.test(this.passwd2)) {
          this.alertModal('密码长度6-20位，且只能包含符号‘_’')
        } else {
          this.password.oldpwd = this.passwd1
          this.password.newpwd = this.passwd2
          const self = this
          axios.post('user/changepwd', self.password, {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            }
          }).then(function (res) {
            if (res.data === '修改密码成功') {
              self.alertModal('修改密码成功')
            } else if (res.data === '旧密码不正确') {
              self.alertModal('旧密码不正确')
            } else {
              self.alertModal('修改密码失败')
            }
          }).catch(function (err) {
            console.log(err)
          })
        }
      },
      /* 编辑文章内容的更新同步 */
      dataEditorFun (data) {
        this.yjAdd.zhengwen = data
      },
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      },
      /* 取消收藏 */
      cancelScFun (id) {
        var result = window.confirm('确认取消收藏吗？')
        if (result) {
          const self = this
          axios.get('CountrysideColl/cancel', id).then(function (res) {
            if (res === '取消成功') {
              self.alertModal('取消收藏成功~')
              self.getPersonShoucang()
            } else {
              self.alertModal('取消收藏失败~')
            }
          }).catch(function (err) {
            self.alertModal('取消收藏失败~')
            console.log('取消收藏错误' + err)
          })
        } else {
          return false
        }
      },
      /* 尚未开启的功能 */
      sorry () {
        this.alertModal('您真细心发现了它,但是该功能还有待开发')
      },
      /* 获取乡村名字 */
      getXcNames () {
        console.log('getXcNames')
        const self = this
        axios.get('country/getCountrysideNames').then(function (res) {
          self.xiangcun = res.data
          self.yjAdd.lishuxiang = self.xiangcun[0]
        }).catch(function (err) {
          console.log(err)
        })
      },
      isLogin () {
        const self = this
        axios.post('user/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }
        ).then(function (res) {
          if (res.data === '未登录') {
            console.log('普通用户也没有登录')
            window.location.href = '/ssm01#/'
          }
          else {
            self.getPersonYouji()
            self.getPersonInfo()
            self.getPersonShoucang()
            self.getXcNames()
          }
        }).catch(function (err) {
          console.log('判断普通用户失败' + err)
          window.location.href = '/ssm01#/'
        })
      }
    },
    created () {
      this.isLogin()
    }
  }
</script>
