<template>
    <div>
      <div class="warp">
        <div>
          <div class="div1"></div>
        </div>
        <div class="transition1 rotateY"><img src="../../../static/toup2.png" alt=""></div>
        <div class="transition4 scale">效果二：放大</div>
        <div class="transition4 rotateScale">效果三：旋转放大</div>
        <div class="transition4 translate">效果四：上下左右移动 </div>
        <div class="div2">
          <button type="button" @click="numChange(1075, 'num1')">点击</button>
          {{num1}}
        </div>
        <div class="div2">
          {{num2}}
        </div>
        <div class="div2" type="button" @click="alertModal('aaaaa')">
          <button>alert模态框</button>
        </div>
        <div class="div2" type="button" @click="alertModal('aaaaa','#/')">
          <button>alert模态框</button>
        </div>
        <div class="div2" type="button" @click="alertTextModal">
          <button>文本</button>
        </div>
      </div>

      <!-- alert模态框 -->
      <div class="modal" v-if="showModalValue === 'modal-alter'">
        <div class="modal-alter">
          <div class="modal-alter-text">
            {{noticeText}}
          </div>
          <div class="modal-alter-btn">
            <button class="alter-btn-close">
              <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
            </button>
          </div>
        </div>
      </div>

      <!-- 文本模态框 -->
      <div class="modal" v-if="showModalValue === 'modal-text'">
      <button class="btn-close" @click="closeTextModal('close')"></button>
      <div class="modal-con">
        <div class="modal-head">
          <span>{{textModalData.title}}</span>
        </div>
        <div class="modal-body" v-html="textModalData.content">
        </div>
        <div class="modal-foot">
        </div>
      </div>
    </div>
    </div>
</template>

<script>
  export default {
    data () {
      return {
        num1: '0,000',
        num2: '0,000',
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        /* ——————————————————————————————————text模态框—————————————————————————————————— */
        textModalData: {
          title: '',
          content: ''
        }
      }
    },
    methods: {
      numChange (num, objStr) {
            console.log('执行了')
            console.log('num:' + num + ' objStr:' + objStr)
            var numI = 0
            var indexNum = 0
            var threeNum = '000'
            const self = this
            if (num <= parseInt((this[objStr]).replace(',', ''))){
                return
            }
            var timer = setInterval(function () {
                /* 获取数据的值 字符串转为数字 */
                var nowNum = parseInt((self[objStr]).replace(',', ''))
                if (numI < num) {
                    indexNum = nowNum + 1
                    if (indexNum >= 1000) {
                        var n = indexNum - Math.floor(indexNum / 1000) * 1000
                        if (n < 10) {
                            threeNum = '00' + n
                        } else if (n < 100) {
                            threeNum = '0' + n
                        } else if (n < 1000) {
                            threeNum = '' + n
                        }
                        self[objStr] = (Math.floor(indexNum / 1000)) + ',' + threeNum
                    } else {
                        if (indexNum < 10) {
                            threeNum = '00' + indexNum
                        } else if (indexNum < 100) {
                            threeNum = '0' + indexNum
                        } else if (indexNum < 1000) {
                            threeNum = '' + indexNum
                        }
                        self[objStr] = '0,' + threeNum
                    }
                    numI++
                }
                if (indexNum === num) {
                    clearInterval(timer)
                }
            },1)
        },
      /* alert模态框1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
        this.showModalValue = 'modal-alter'
      },
      /* text模态框1 */
      closeTextModal () {
        this.showModalValue = ''
      },
      /* text模态框1 */
      alertTextModal () {
        this.showModalValue = 'modal-text'
      }
    },
    created () {
//        this.numChange(0,'num2')
    }
  }
</script>

<style scoped>
  @import "../../styles/model.css";
    .warp{
        width: 100%;
        height: 400px;
        background: palevioletred;
        float: none;
    }
    .div1{
        background: transparent;
        width: 0px;
        height: 0px;
        border-left: 30px solid transparent;
        border-right: 30px solid blue;
        border-bottom: 30px solid transparent;
        border-top: 30px solid transparent;
    }
    .div2{
        color: black;
        font-weight: bold;
        font-size: large;
    }
    .warp div{
        width:120px;
        height:120px;
        line-height:120px;
        margin: 20px;
        background-color: #5cb85c;
        float: left;
        font-size: 12px;
        text-align: center;
        color:orangered;
    }
    .transition1 {
        transition: All 1s ease-in-out;
        -webkit-transition: All 1s ease-in-out;
        -moz-transition: All 1s ease-in-out;
        -o-transition: All 1s ease-in-out;
    }
    .transition4 {
        transition: All 0.4s ease-in-out;
        -webkit-transition: All 0.4s ease-in-out;
        -moz-transition: All 0.4s ease-in-out;
        -o-transition: All 0.4s ease-in-out;
    }

    /*效果一：360°旋转 修改rotate(旋转度数)*/
    .rotateY:hover {
        transform: rotateY(180deg);
        -webkit-transform: rotateY(180deg);
        -moz-transform: rotateY(180deg);
        -o-transform: rotateY(180deg);
        -ms-transform: rotateY(180deg);
    }
    /*效果二：放大 修改scale(放大的值)*/
    .scale:hover {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
        -moz-transform: scale(1.2);
        -o-transform: scale(1.2);
        -ms-transform: scale(1.2);
    }
    /*效果三：旋转放大 修改rotate(旋转度数) scale(放大值)*/
    .rotateScale:hover {
        transform: rotate(360deg) scale(1.2);
        -webkit-transform: rotate(360deg) scale(1.2);
        -moz-transform: rotate(360deg) scale(1.2);
        -o-transform: rotate(360deg) scale(1.2);
        -ms-transform: rotate(360deg) scale(1.2);
    }
    /*效果四：上下左右移动 修改translate(x轴,y轴)*/
    .translate:hover {
        transform: translate(0, -10px);
        -webkit-transform: translate(0, -10px);
        -moz-transform: translate(0, -10px);
        -o-transform: translate(0, -10px);
        -ms-transform: translate(0, -10px);
    }
</style>



