<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span :class="{active: show === 'yjMine'}"><a href="javascript:;" @click="showTab('yjMine')">我的游记</a></span>
      <span :class="{active: show === 'yjAdd'}"><a href="javascript:;" @click="showTab('yjAdd')">发布游记</a></span>
    </div>
    <!-- 我的游记 -->
    <div class="midr2 content" v-if=" show === 'yjMine' ">
      <div class="cell" v-for="(yj, index) in youji">
        <router-link :to="{path: '/show/youjiShow', query: { id: yj.idarticle, content: yj.content, author: userInfo.userName, time: yj.articletime, name: yj.name, mainpic: yj.mainpic }}" target="_blank">
          <img :src="'img/src/' + yj.mainpic" alt="">
          <span class="title">{{yj.name}}</span>
          <br>
          <span class="time" v-text="yj.articletime"></span>
        </router-link>
      </div>
      <!--<a href="##">删除</a></span>-->
    </div>
    <!-- 游记添加 -->
    <div class="midr3 content" v-if=" show === 'yjAdd' ">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>标题：</span>
          <input type="text" class="text-input" style="width: 500px;" v-model="yjAdd.title">
        </p>
        <p class="formtr formtrp">
          <span>隶属：</span>
          <select name="" id="location1" class="select" style="width: 96px; margin-right:5px;">
            <option value="1">浙江省</option>
          </select>
          <select name="" id="location2" class="select" style="width: 96px; margin-right:5px;">
            <option value="1">湖州市</option>
          </select>
          <select name="" id="location3" class="select" style="width: 96px; margin-right:5px;">
            <option value="1">安吉县</option>
          </select>
          <select autocomplete="off" name="" id="location4" class="select" style="width: 110px; margin-right:5px;" v-model="yjAdd.lishuxian">
            <option value="递铺镇">递铺镇</option>
            <option value="杭垓镇">杭垓镇</option>
            <option value="孝丰镇">孝丰镇</option>
            <option value="皈山乡">皈山乡</option>
            <option value="良朋镇">良朋镇</option>
            <option value="鄣吴镇">鄣吴镇</option>
            <option value="高禹镇">高禹镇</option>
            <option value="梅溪镇">梅溪镇</option>
            <option value="溪龙乡">溪龙乡</option>
            <option value="昆铜乡">昆铜乡</option>
            <option value="章村镇">章村镇</option>
            <option value="报福镇">报福镇</option>
            <option value="上野乡">上野乡</option>
            <option value="天荒坪镇">天荒坪镇</option>
            <option value="山川乡">山川乡</option>
          </select>
          <select name="" id="location5" class="select" style="width: 110px" v-model="yjAdd.lishuxiang">
            <option value="余村村">余村村</option>
            <option value="双一村">双一村</option>
            <option value="横山坞村">横山坞村</option>
            <option value="刘家塘村">刘家塘村</option>
            <option value="马家弄村">马家弄村</option>
            <option value="高家堂村">高家堂村</option>
            <option value="尚书干村">尚书干村</option>
            <option value="长林垓村">长林垓村</option>
          </select>
        </p>
        <div class="formtr formtrDiv clear">
          <span>封面：</span>
          <div class="xc_cover">请选择封面图片</div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input">
            <w>请选择图片</w>
            <input type="file" class="file-btn-input" id="" multiple="multiple" @change="setImagePreviews1('file-btn-input','xc_cover',0)">
          </a>
        </div>
        <div class="formtr formtrDiv">
          <span>正文：</span>
          <div class="vueEditor">
            <vue-html5-editor @change="updateData" :content="content" :height="500" :z-index="1000" :auto-height="true"></vue-html5-editor>
          </div>
          <!-- <textarea name="" id="" cols="80" rows="30"></textarea> -->
        </div>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="发表" class="btn" @click="sendYouji2">
          <router-link :to="{path: '/show/youjiShow2', query: { title: yjAdd.title, lishuxian: yjAdd.lishuxian, lishuxiang: yjAdd.lishuxiang, fengmian: yjAdd.fengmian, zhengwen: yjAdd.zhengwen, author: userInfo.userName }}" class="btn" target="_blank">
            预览
          </router-link>
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'yjMine',
        content: 'hello',
        /* 前后 我的游记 */
        youji: [
          {
            idarticle: 1,
            name: '故地重游 余村',
            articletime: '2017-7-2',
            mainpic: '../../static/view.jpg',
            content: 'youji1'
          },
          {
            idarticle: 2,
            name: '我的最美乡村',
            articletime: '2017/7/2',
            mainpic: '../../static/view2.jpg',
            content: 'youji2'
          }
        ],
        /* 前台 新建游记 */
        yjAdd: {
          title: '',
          lishuxian: '递铺镇',
          lishuxiang: '余村村',
          fengmian: '',
          zhengwen: ''
        },
        /* 后台 新建游记 sendData */
        sendData: new window.FormData()
      }
    },
    props: ['userInfo'],
    methods: {
      showTab (str) {
        this.show = str
      },
      /* 编辑器内容同步 */
      updateData (data) {
        this.yjAdd.zhengwen = data
      },
      /* 封面图片添加函数 */
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.yjAdd.fengmian = imgObjPreview.src
          this.sendData.set('mainPic', choosePic.files[0], choosePic.files[0].name)
        }
      },
      /* 获取个人游记 引dateFormat */
      getPersonYouji2 () {
        const self = this
        axios.get('userarticle/UserArticles').then(function (res) {
          self.youji = res.data.articles
          for (var i = 0; i < self.youji.length; i++) {
            self.youji[i].articletime = self.dateFormat(self.youji[i].articletime)
          }
          console.log('第二获取个人游记成功！')
        }).catch(function (err) {
          console.log('第二获取个人游记失败：' + err)
        })
      },
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      },
      /* 新建游记 */
      sendYouji2 () {
        this.sendData.set('name', this.yjAdd.title)
        this.sendData.set('location', '浙江省湖州市安吉县' + this.yjAdd.lishuxian + this.yjAdd.lishuxiang)
        this.sendData.set('content', this.yjAdd.zhengwen)
        console.log('要提交的数据是： { 标题: ' + this.sendData.get('name') + ' ,隶属: ' + this.sendData.get('location') + ' ,封面: ' + this.sendData.get('mainPic') + ' ,正文： ' + this.sendData.get('content') + ' }')
        axios.post('userarticle/newarticle2', this.sendData, {
          headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data === '发表成功') {
            window.alert('发布游记成功')
            this.getPersonYouji()
            /* window.location.reload() */
          } else {
            window.alert('发布游记失败')
          }
        }).catch(function (err) {
          console.log(err)
        })
      }
    },
    created () {
      this.getPersonYouji2()
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";

  .midr .midr2 .cell {
    width: 25%;
    height: 260px;
    float: left;
    padding: 20px 20px 0px 0px;
    box-sizing: border-box;
  }

  .midr .midr2 .cell img {
    width: 100%;
    height: 180px;
  }

  .midr .midr2 .cell .title {
    width: 95%;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 5px;
  }

  .midr .midr2 .cell .time {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-left: 5px;
    font-size: small;
  }

  .midr .midr2 .cell .time span a {
    color: #ccc;
    display: inline-block;
    float: right;
    font-size: small;
  }
</style>




