<template>
	<div>
	  <div class="hg_yellow"></div>
    <!-- 标题 -->
    <div class="tn_top_box">
      <div class="tn_topz_center">
        <div class="tn_title" v-text="tnTitle"></div>
        <div class="tn_other_infor">
          <span style="margin-right:10px;display:inline;" v-text="makeTime"></span>
          <span>By</span>
          <span v-text="byUser"></span>
          <span><img style="height: 30px;width: 30px" src="static/like.png" alt="" @click="changeNowShow('like',0)" v-show="!showNowLike"></span>
          <span><img style="height: 30px;width: 30px" src="static/likeA.png" alt="" @click="changeNowShow('like',1)" v-show="showNowLike"></span>
          <span v-text="likers"></span>
        </div>
      </div>
    </div>
    <!-- 展示 -->
    <div class="show_tn_main">
      <!-- 背景图片 -->
      <img :src="showNowPic" alt="" style="width: 100%;height: 100%;filter: blur(20px);">
      <!-- 图片 -->
      <img :src="showNowPic" alt="" class="showing_img animated fadeIn" v-if="showFade1">
      <img :src="showNowPic" alt="" class="showing_img animated fadeIn" v-if="showFade2">
      <!-- 按钮、文字 -->
      <div class="img_infor_box">
        <!-- 第几天 -->
        <div class="one_collection_tab">
          <ul v-for="(oneDay, index1) in tnInfor" :key="index1">
            <li :class="{now_day_checked: showNowDay === index1, not_now_day: showNowDay != index1, day_tab: true}" @click="changeNowShow('day',index1)">Day{{index1 + 1}}</li>
            <li  v-for="(oneImg, index2) in oneDay.imges" :key="index2" :class="{now_pic_checked: showNowPicNum === index1 + '0' + index2, not_now_pic: showNowPicNum != index1 + '0' + index2, pic_tab: true}" @click="changeNowShow('pic',index2,index1)"></li>
          </ul>
        </div>
        <!-- 按钮 -->
        <div class="change_next_pre">
          <div class="pre_img"><img src="static/icons/left_img1.png" alt="" class="img_auto" @click="perPage()"></div>
          <div class="next_img"><img src="static/icons/right_img1.png" alt="" class="img_auto" @click="nextPage()"></div>
        </div>
        <!-- 文字信息 -->
        <div class="shadow_box">
          <div class="center_cotain">
            <div class="today_discribe" v-text="showNowDiscribe"></div>
          </div>
        </div>
      </div>
    </div>
	</div>
</template>
<script>
  export default {
    data () {
      return {
        showFade1: false,
        showFade2: true,
        showNowPic: 'static/background/c0.jpg',
        showNowLike: false,
        likers: 22,
        byUser: '一个人的粉红豹',
        makeTime: '2017-09-14',
        tnTitle: '我有故事，你有酒吗？',
        tnInfor: [
          {
            describe: '这里还有五指山的国家级自然保护区、仕阶旅游区、蝴蝶生态牧场、牙胡梯田、黎家第一村初保村、太平山瀑布、百花岭瀑布；琼中县的鹦哥岭、百花岭瀑布、黎母山国家森林公园、天上什寒什寒村；屯昌县的木色湖、枫木鹿场、白鹭乐园', imges: ['static/background/c0.jpg', 'static/background/c1.jpg', 'static/background/c2.jpg', 'static/background/c5.jpg', 'static/background/c1.jpg', 'static/background/c2.jpg', 'static/background/c5.jpg']
          },
          {
            describe: '牙胡梯田、黎家第一村初保村、太平山瀑布、百花岭瀑布；琼中县的鹦哥岭、百花岭瀑布、黎母山国家森林公园、天上什寒什寒村；屯昌县的木色湖、枫木鹿场、白鹭乐园', imges: ['static/background/c3.jpg', 'static/background/c4.jpg']
          },
          {
            describe: '牙胡梯田、黎家第一村初保村、太平山瀑布、百花岭瀑布；琼中县的鹦哥岭、百花岭瀑布、黎母山国家森林公园、天上什寒什寒村；屯昌县的木色湖、枫木鹿场、白鹭乐园', imges: ['static/background/c3.jpg', 'static/background/c4.jpg']
          },
          {
            describe: '牙胡梯田、黎家第一村初保村、太平山瀑布、百花岭瀑布；琼中县的鹦哥岭、百花岭瀑布、黎母山国家森林公园、天上什寒什寒村；屯昌县的木色湖、枫木鹿场、白鹭乐园', imges: ['static/background/c3.jpg', 'static/background/c4.jpg']
          }
        ],
        showNowDay: 0,
        showNowPicNum: '000',
        showNowDiscribe: '这里还有五指山的国家级自然保护区、仕阶旅游区、蝴蝶生态牧场、牙胡梯田、黎家第一村初保村、太平山瀑布、百花岭瀑布；琼中县的鹦哥岭、百花岭瀑布、黎母山国家森林公园、天上什寒什寒村；屯昌县的木色湖、枫木鹿场、白鹭乐园'
      }
    },
    methods: {
      changeNowShow (str, num, indexfa) {
        if (str === 'like') {
          this.showNowLike = !this.showNowLike
          if (num === 0) {
            this.likers ++
          } else {
            this.likers --
          }
        } else if (str === 'day') {
          this.showNowDay = num
          this.showNowPicNum = num + '00'
          this.showNowPic = this.tnInfor[num].imges[0]
          this.showNowDiscribe = this.tnInfor[num].describe
        } else if (str === 'pic') {
          this.showNowDay = indexfa
          this.showNowPicNum = indexfa + '0' + num
          this.showNowPic = this.tnInfor[indexfa].imges[num]
          this.showNowDiscribe = this.tnInfor[indexfa].describe
        }
      },
      perPage () {
        var nowPicArr = this.showNowPicNum.split('')
        var nowDN = parseInt(nowPicArr[0])
        var nowPN = parseInt(nowPicArr[2])
        console.log(this.showNowPicNum)
        if (nowPN > 0) {
          nowPN--
        } else {
          if (nowDN > 0) {
            nowDN--
            nowPN = this.tnInfor[nowDN].imges.length - 1
          } else {
            nowDN = this.tnInfor.length - 1
            nowPN = this.tnInfor[nowDN].imges.length - 1
          }
        }
        this.showNowDay = nowDN
        this.showNowPicNum = nowDN + '0' + nowPN
        this.showNowPic = this.tnInfor[nowDN].imges[nowPN]
        this.showNowDiscribe = this.tnInfor[nowDN].describe
        this.showFade1 = !this.showFade1
        this.showFade2 = !this.showFade2
      },
      nextPage () {
        var nowPicArr = this.showNowPicNum.split('')
        var nowDN = parseInt(nowPicArr[0])
        var nowPN = parseInt(nowPicArr[2])
        console.log(this.showNowPicNum)
        if (nowPN < this.tnInfor[nowDN].imges.length - 1) {
          nowPN++
        } else {
          if (nowDN < this.tnInfor.length - 1) {
            nowDN++
          } else {
            nowDN = 0
          }
          nowPN = 0
        }
        this.showNowDay = nowDN
        this.showNowPicNum = nowDN + '0' + nowPN
        this.showNowPic = this.tnInfor[nowDN].imges[nowPN]
        this.showNowDiscribe = this.tnInfor[nowDN].describe
        this.showFade1 = !this.showFade1
        this.showFade2 = !this.showFade2
      }
    }
  }
</script>
<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/animate.css";

  .img_auto{
    width: 100%;
    height: 100%;
  }
  .hg_yellow{
    height: 5px;
    width: 100%;
    background: #FFDE3A;
  }
/* 顶部标题等处 */
  .tn_top_box{
    height: 80px;
    width: 100%;
  }
  .tn_topz_center{
    height: 100%;
    width: 1000px;
    margin: 0 auto;
    position: relative;
  }
  .tn_title{
    height: 80px;
    line-height: 90px;
    margin:  0 auto;
    width: 350px;
    text-align: center;
    font-size: 20px;
  }
  .tn_other_infor{
    /* background: lightblue; */
    width:300px;
    height: 80px;
    line-height: 90px;
    position: absolute;
    right: 0;
    top: 0;
  }
  .tn_other_infor span {
    font-size: 14px;
    color: #c1c1c1;
  }
   .tn_other_infor span img{
     vertical-align: middle;
     margin: -10px 0px 0px 10px;
     cursor: pointer;
   }
/* 展示块 */
  .show_tn_main{
    width: 100%;
    height: 600px;
    position: relative;
  }
  /* 图片 */
  .showing_img{
    width: 800px;
    height: 400px;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  /* 图片信息 */
  .img_infor_box{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0px;
    left:0px;
  }
  .one_collection_tab{
    height: 60px;
    /* line-height: 80px; */
    width: 500px;
    margin: 20px 0px 0px 50px;
    /* text-align: center; */
    /* background: silver; */
  }
  .one_collection_tab ul{
    float: left;
  }
  .pic_tab{
    width: 10px;
    height: 10px;
    margin: 5px 10px 0px 0px;
    border-radius: 50%;
    font-size: 12px;
    /* line-height: 20px; */
    text-align: center;
    float: left;
    cursor: pointer;
  }
  .day_tab{
    margin-right: 10px;
    height: 20px;
    line-height: 20px;
    float: left;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
  }
  .now_pic_checked{
    background: #FFDE3A;
    color: #fff;
  }
  .not_now_pic{
    background: rgba(255,255,255,0.3);
    color: rgba(255,255,255,0.3);
  }
  .now_day_checked{
    color: #FFDE3A;
  }
  .not_now_day{
    color: rgba(255,255,255,0.7);
  }
  .change_next_pre{
    width: 100%;
    height: 80px;
    margin: 180px 0px 0px;
    /* background: mediumspringgreen; */
  }
  .pre_img{
    height: 50px;
    width: 50px;
    float: left;
    margin: 15px 0px 0px 50px;
    cursor: pointer;
  }
  .next_img{
    height: 50px;
    width: 50px;
    float: right;
    margin: 15px 50px 0px 0px;
    cursor: pointer;
  }
  .shadow_box{
    min-height: 10px;
    width: 100%;
    position: absolute;
    bottom: 0px;
    background: rgba(0,0,0,0.5);
    padding: 20px 0px;
  }
  .center_cotain{
    margin: 0 auto;
    width: 1000px;
  }
  .today_discribe{
    width: 100%;
    color: #fff;
  }

  

</style>



