<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span :class="{active: show === 'tab1'}"><a href="javascript:;" @click="showTab('tab1')">修改个人信息</a></span>
      <span :class="{active: show === 'tab2'}"><a href="javascript:;" @click="showTab('tab2')">修改密码</a></span>
      <span :class="{active: show === 'tab3'}"><a href="javascript:;" @click="showTab('tab3')">个人认证</a></span>
    </div>
    <!-- 修改个人资料 -->
    <div class="midr3 content" v-if="show === 'tab1'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>昵称：</span>
          <input type="text" class="text-input" v-model="userInfo2.username">
        </p>
        <div class="formtr formtrDiv clear">
          <span>头像：</span>
          <div class="xc_cover2">
            <img :src="userInfo2.headPic" alt="">
          </div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input">
            <p>请选择图片</p>
            <input type="file" class="file-btn-input"  @change="imageInputFun('file-btn-input','xc_cover2',0,'userInfo2','userPic')">
          </a>
        </div>
        <p class="formtr formtrp">
          <span>性别：</span>
          <input type="radio" value="0" name="sex" :checked="userInfo2.sex === '男'" @click="userInfo2.sex='男'">
          <label class="input_radio" for="sex">男</label>
          <input type="radio" value="1" name="sex" :checked="userInfo2.sex === '女'" @click="userInfo2.sex='女'">
          <label class="input_radio" for="sex">女</label>

        </p>
        <!--<p class="formtr formtrp">
          <span>生日：</span>
          <input type="text" class="text-input" placeholder="2017-01-01" v-model="userInfo2.birth">
        </p>-->
        <p class="formtr formtrp">
          <span>手机：</span>
          <input type="text" class="text-input" v-model="userInfo2.phone">
        </p>
        <p class="formtr formtrp">
          <span>证件号：</span>
          <input type="text" class="text-input" v-model="userInfo2.idcard" disabled="disabled">
        </p>
        <!--<p class="formtr formtrp">
          <span>微信：</span>
          <input type="text" class="text-input" v-model="userInfo2.wechat">
        </p>-->
        <p class="formtr formtrp">
          <span>居住城市：</span>
          <input type="text" class="text-input" placeholder="上海"  v-model="userInfo2.city">
        </p>
        <!--<p class="formtr formtrp">
          <span>个性签名：</span>
          <input type="text" class="text-input" v-model="userInfo2.signature">
        </p>-->
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendDataFun">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <!-- 修改密码 -->
    <div class="midr2 content" v-if="show === 'tab2'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>旧密码：</span>
          <input type="password" class="text-input" v-model="passwd1">
        </p>
        <p class="formtr formtrp">
          <span>新密码：</span>
          <input type="password" class="text-input" v-model="passwd2">
        </p>
        <p class="formtr formtrp">
          <span>再次输入：</span>
          <input type="password" class="text-input" v-model="passwd3">
        </p>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendPasswd">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <!-- 个人认证 -->
    <div class="midr4 content" v-if="show === 'tab3'">
      <!-- 已认证 -->
      <div v-if="rz.number !== ''" class="renzhen">
        已认证过~
      </div>
      <div v-if="rzValue2 === true" class="renzhen">
        待审核~
      </div>
      <!-- 未认证 -->
      <form v-if="rzValue === false" action="##" class="form">
        <p class="formtr formtrp">
          <span class="must" style="width: 2px">*</span></span><span>真实姓名：</span>
          <input type="text" class="text-input" v-model="rz.name">
        </p>
        <p class="formtr formtrp">
          <span class="must" style="width: 2px">*</span><span>证件号：</span>
          <input type="text" class="text-input" v-model="rz.number">
        </p>
        <div class="formtr formtrDiv clear">
          <span class="must" style="width: 2px">*</span><span>身份证正面：</span>
          <div class="xc_cover rz-pic1" style="margin: 0px 0px 25px 140px;">
            请选择身份证正面
          </div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input" style="margin-left: 20px">
            <p>请选择图片</p>
            <input type="file" class="file-btn-input rz-pic-btn1" id="" multiple="multiple" @change="imageInputFun('rz-pic-btn1','rz-pic1',0,'rz','rzpic1')">
          </a>
        </div>
        <div class="formtr formtrDiv clear">
          <span class="must" style="width: 2px">*</span><span>身份证反面：</span>
          <div class="xc_cover rz-pic2" style="margin: 0px 0px 25px 140px;">
            请选择身份证反面
          </div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input" style="margin-left: 20px">
            <p>请选择图片</p>
            <input type="file" class="file-btn-input rz-pic-btn2" id="" multiple="multiple" @change="imageInputFun('rz-pic-btn2','rz-pic2',0,'rz','rzpic2')">
          </a>
        </div>
        <div class="formtr formtrDiv">
          <span class="must" style="width: 2px">&nbsp;</span><span>备注：</span>
          <textarea class="textarea" rows="13" v-model="rz.beizhu" style="width:63.5%;"></textarea>
        </div>
        <div class="formtr formtrDiv clear">
          <span class="must" style="width: 2px">&nbsp;</span><span>备注图片：</span>
          <div class="xc_cover rz-pic3" style="margin: 0px 0px 25px 140px;">请选择备注图片</div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input" style="margin-left: 20px">
            <p>请选择图片</p>
            <input type="file" class="file-btn-input rz-pic-btn3" id="" multiple="multiple" @change="imageInputFun('rz-pic-btn3','rz-pic3',0,'rz','rzpic3')">
          </a>
        </div>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendRz">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <div style="clear:both"></div>
    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        rzValue2:false,
        rzValue: false,
        show: 'tab1',
        passwd1: '',
        passwd2: '',
        passwd3: '',
        /* 页面显示的个人资料 */
        userInfo3: {
          userName: '阿水12344',
          signature: '这位同学很懒，木有签名的说～',
          userPic: '../../../static/t16.jpg',
          dengji: 1,
          fensi: 2,
          guanzu: 3,
          jingyan: 4,
          sex: '男',
          birthday: '2017-05-20',
          city: '上海',
          phone: 'aaa',
          wechat: 'aaa',
          idcard: 'ss'
        },
        /* 临时存放 个人资料 */
        userInfo2: {
          username: '无名',
          sex: '男',
          birth: '2017-01-01',
          city: '杭州',
          signature: '无',
          headPic: '../../../static/mrtx.jpg',
          phone: '1782680****',
          wechat: 'wm1782680****',
          level: '0',
          fans: '0',
          attention: '0',
          experience: '0',
          idcard: 'aaa'
        },
        /* 后台 密码 */
        password: {
          oldpwd: '',
          newpwd: ''
        },
        /* 后台 个人资料 表单格式 param */
        param: new window.FormData(),
        /* 后台 认证 */
        paramRZ: new window.FormData(),
        rz: {
          name: '',
          number: '',
          rzpic1: '',
          rzpic2: '',
          rzpic3: '',
          beizhu: ''
        }
      }
    },
    methods: {
      /* alert模态框1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
            this.showModalValue = 'modal-alter'
      },
      showTab (str) {
        this.show = str
      },
      /* 修改密码 */
      sendPasswd () {
        if (this.passwd2 !== this.passwd3) {
          this.alertModal('两次输入的密码不相同！')
        } else if (!/^([a-zA-Z0-9]|[_]){6,20}$/.test(this.passwd2)) {
          this.alertModal('密码长度6-20位，且只能包含符号‘_’')
        } else {
          this.password.oldpwd = this.passwd1
          this.password.newpwd = this.passwd2
          axios.post('CountrysideUser/changepwd', this.password, {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            }
          }).then(function (res) {
            if (res.data === '修改密码成功') {
              self.alertModal('修改密码成功')
            } else if (res.data === '旧密码不正确') {
              self.alertModal('旧密码不正确')
            } else {
              self.alertModal('修改密码失败')
            }
          }).catch(function (err) {
            console.log(err)
          })
        }
      },
      /* 获取个人资料 */
      getData () {
        console.log('成功从person父组件中取到数据')
        this.userInfo2.username = this.userInfo.userName
        this.userInfo2.sex = this.userInfo.sex
        console.log(this.userInfo2.sex)
        this.userInfo2.city = this.userInfo.city
        this.userInfo2.headPic = this.userInfo.userPic
        this.userInfo2.phone = this.userInfo.phone
        this.userInfo2.idcard = this.userInfo.idcard
        this.rz.number = this.userInfo.idcard
      },
      /* 修改个人资料 */
      sendDataFun () {
        this.param.set('username', this.userInfo2.username)
        this.param.set('sex', this.userInfo2.sex)
        // this.param.set('birth', this.userInfo2.birth)
        this.param.set('city', this.userInfo2.city)
        // this.param.set('signature', this.userInfo2.signature)
        this.param.set('phone', this.userInfo2.phone)
        // this.param.set('wechat', this.userInfo2.wechat)
        console.log('要上传的数据：{用户名：' + this.param.get('username') + ',性别： ' + this.param.get('sex') + ',城市： ' + this.param.get('city') + ',头像： ' + this.param.get('headpic') + ',手机： ' + this.param.get('phone'))
        console.log('头像：')
        if (this.param.get('headpic') === null) {
          console.log('didn’t send the mainPic' )
          this.param.set('headpic','')
        }
        console.log(this.param.get('headpic'))
        const self = this
        axios.post('CountrysideUser/changeInfo', self.param, {headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data.info) {
            console.log('change user info success')
            self.getPersonInfo()
          }
        }).catch(function (err) {
          console.log(err)
          console.log('failed to change userInfo')
        })
      },
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      },
      /* 上传单张图片 */
      imageInputFun (class1, class2, index, obj1, obj2) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          if (/^((?!image).)+/g.test(choosePic.files[i].type)) {
            window.alert('请选择正确的图片格式！')
            i = 99999
          } else {
            if (obj1 === 'userInfo2') {
              showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
              this.param.set('headpic', choosePic.files[0], choosePic.files[0].name)
            } else {
              showPic.innerHTML += "<img class='img" + class2 + i + "' style='margin-top: -40px;width:100%;height:100%;' />"
              this.paramRZ.set(obj2, choosePic.files[0], choosePic.files[0].name)
            }
            var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
            imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
            this[obj1][obj2] = imgObjPreview.src
          }
        }
      },
      /* 个人认证 （需要修改） */
      sendRz () {
        this.paramRZ.set('name', this.rz.name)
        this.paramRZ.set('number', this.rz.number)
        this.paramRZ.set('beizhu', this.rz.beizhu)
        const self = this
        axios.post('Authentication/addAutn',self.paramRZ, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
          }
        }).then(function (res) {
          self.rzValue2 = true
          console.log('认证成功' + res)
        }).catch(function (err) {
          self.rzValue2 = true
          console.log('认证失败' + err)
        })
      }
    },
    props: ['userInfo'],
    created () {
      this.getData()
//      this.getPersonInfo()
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import '../../styles/model.css';
  .renzhen{
    width: 80%;
    height: 150px;
    line-height: 150px;
    padding-left: 100px;
    background: #eee;
    box-sizing: border-box;
  }
</style>



