<template>
	<div>
	  <router-view :message="message"></router-view>
      <div class="footer">
	    © 2017 ZheJiang. All Rights Reserved | Design by Us.
	  </div>
	</div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        message: {
          userName: '',
          userPic: '',
          // 判断需要执行普通用户吗
          doAxios2: true
        }
      }
    },
    methods: {
      getUser () {
        const self = this
        /* 1、判断商业用户是否登入 */
        axios.post('CountrysideUser/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }
        ).then(function (res) {
          if (res.data === '未登录') {
            console.log('show,商业用户没有登录')
          } else {
            // 商业用户已登录
            self.message.doAxios2 = false
            axios.get('CountrysideUser/info'
            ).then(function (res) {
              console.log('show，商业用户数据：')
              self.message.userName = res.data.countrysideUser[0].name
              self.message.userPic = res.data.countrysideUser[0].headpic
            }
            ).catch(function (err) {
              console.log('show，商业用户数据失败：' + err)
            })
          }
        }
        ).catch(function (err) {
          console.log('show,商业用户判断404：' + err)
        })
        if (this.message.doAxios2) {
          // 判断普通用户是否登入 （商业用户未登录）
          axios.post('user/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }
          ).then(function (res) {
            if (res.data === '未登录') {
              console.log('show,普通用户没有登录')
            } else {
              // 普通用户已登录
              axios.get('user/info').then(function (res) {
                console.log('show，普通用户数据：')
                self.message.userName = res.data.user[0].username
                self.message.userPic = 'img/headpic/' + res.data.user[0].headpic
              }).catch(function (err) {
                console.log('show，普通用户数据失败：' + err)
              })
            }
          }
          ).catch(function (err) {
            console.log('show,普通用户判断404：' + err)
          })
        }
      }
    },
    created () {
      this.getUser()
    }
  }
</script>
<style scoped>
.footer{
  width: 100%;
  height: 120px;
  background: #000;
  color: #fff;
  text-align: center;
  line-height: 120px;
}
</style>



