<template>
  <div>
  <!-- 封面 -->
		<div class="cover">
			<img :src="rCover">
			<div class="pictures" @click="showChange('modal-img')">查看图片</div>
		</div>

		<div class="main clear">
			<div class="main_left">
			<!-- 回到顶部 -->
				<div class="backToTop" @click="scrollUpFun()">
                    <img src="../../../static/toup2.png">
                </div>
			<!-- 定位 -->
				<div class="located">
					<ul>
						<li style="border-bottom: 3px solid #FFDE3A;" @click="scrollFun(500)">综诉</li>
						<li @click="scrollFun(1500)">位置</li>
						<li @click="scrollFun(2050)">评价</li>
					</ul>
				</div>
			<!-- 名称栏 -->
				<div class="namecon">
					<div class="na_sc clear">
						<div class="spo_name">{{rName}}</div>
						<div class="spo_score">
							<ul>
                <li v-if=" 1 <= rScore ">
                  <img src="../../../static/icons/flo_s.png"></li>
                <li v-if=" 2 <= rScore ">
                  <img src="../../../static/icons/flo_s.png"></li>
                <li v-if=" 3 <= rScore ">
                  <img src="../../../static/icons/flo_s.png"></li>
                <li v-if=" 4 <= rScore ">
                  <img src="../../../static/icons/flo_s.png"></li>
                <li v-if=" 5 <= rScore ">
                  <img src="../../../static/icons/flo_s.png"></li>
                <li v-if=" 1 > rScore ">
                  <img src="../../../static/icons/flo_k.png"></li>
                <li v-if=" 2 > rScore ">
                  <img src="../../../static/icons/flo_k.png"></li>
                <li v-if=" 3 > rScore ">
                  <img src="../../../static/icons/flo_k.png"></li>
                <li v-if=" 4 > rScore ">
                  <img src="../../../static/icons/flo_k.png"></li>
                <li v-if=" 5 > rScore ">
                  <img src="../../../static/icons/flo_k.png"></li>
              </ul>
							<div>{{rScore}}.0分</div>
						</div>
					</div>
					<div class="short_addr clear">浙江省&nbsp;&nbsp;湖州市&nbsp;&nbsp;安吉县&nbsp;&nbsp;{{rbelongTo}}</div>
				</div>
			<!-- 简介 -->
				<div class="introduce clear">
					<ul>
						<li>简介：</li>
						<li>{{rIntroduction}}</li>
					</ul>
				</div>
			<!-- 联系方式 -->
				<div class="contact clear">
					<ul>
						<li>联系方式：</li>
						<li>{{rContact}}</li>
					</ul>
				</div>
			<!-- 视频 -->
				<div class="spo_video clear">
					<video width="100%" height="100%" controls>
		                <source :src="rVideo"  type="video/mp4">
		            </video>
				</div>
				<div class="hengg1 clear"></div>
			<!-- 详细地址 -->
				<div class="address clear">
					<ul>
						<li>详细地址：</li>
						<li>{{rAddress}} </li>
					</ul>
				</div>
			<!-- 位置显示 -->
				<div class="map"></div>
				<div class="hengg1 clear"></div>
			</div>

		<!-- 右侧 -->
			<div class="main_right">
				<div class="price">人均&nbsp; &nbsp; <span style="font-size:20px;font-weight:bold;">{{rPrice}}RMB</span>&nbsp; &nbsp; 每次</div>
				<div class="support">
					<div class="sup_btn" v-if="good_show === 'good'" @click="ChangeGood('gooded', 1)">
						<img src="../../../static/icons/good.png">
						喜欢就要行动
					</div>
					<div class="sup_btn" v-if="good_show === 'gooded'" @click="ChangeGood('good', -1)">
						<img src="../../../static/icons/gooded.png">
						喜欢就要行动
					</div>
					<div class="sup_count">
						<span>{{rLikers}}</span>
						位游客做出了行动
					</div>
					<div class="share">
						<img src="../../../static/icons/wechated.png">
						<img src="../../../static/icons/sinaed.png">
						<img src="../../../static/icons/mailed.png">
						<img src="../../../static/icons/pointes.png">
					</div>
				</div>
				<div class="wrong">
					<img src="../../../static/icons/send.png">
					<router-link :to="{ path: '/show/finderror' }" class="aFont" target="_blank">举报该餐馆</router-link>
				</div>
			</div>
		</div>
		<!--图片模态框-->
    <div class="modal" v-if="showModalValue === 'modal-img'">
      <div class="modal-con modal-con-noColor">
        <div class="modal-con-img">
          <button class="btn-close" @click="showChange('close')"></button>
          <div class="modal-img-btn modal-img-btn-left" @click="showBefore()" @mouseenter="showBtn()" @mouseleave="showBtn()" :class="{showBtnClass: showButtonValue === true}"></div>
          <!--<img src="../../images/view.jpg" alt="">-->
          <img v-for="(image, index) in imgInfo" :src="image.src" v-if="showImgIndex === index">
          <!--文字信息-->
          <!--<div class="imgName">
            <transition name="slide-fade">
              <p v-for="(image, index) in imgInfo" v-if="showImgIndex === index" v-text="image.name">hello</p>
            </transition>
          </div>-->
          <div class="modal-img-btn modal-img-btn-right" @click="showAfter()"  @mouseenter="showBtn()" @mouseleave="showBtn()" :class="{showBtnClass: showButtonValue === true}"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        good_show: 'good',
        liker: 20,
        scroll: 0,
        startLocation: 0,
        showModalValue: '',
        showButtonValue: false,
        showImgIndex: 0,    /* 当前显示的图片的索引号 */
        imgInfo: [
          {src: '/static/special1.jpg', name: '安吉白茶1'},
          {src: '/static/special2.jpg', name: '安吉白茶2'},
          {src: '/static/special3.jpg', name: '安吉白茶3'},
          {src: '/static/special4.jpg', name: '安吉白茶4'},
          {src: '/static/special5.jpg', name: '安吉白茶5'}
        ],
        rName: '大饭店',
        rScore: 3,
        rbelongTo: '高家堂村',
        rIntroduction: '酒店位于海拨1000米的山顶，拥有连绵的竹海景观，空气含有大量富氧离子，让人心旷神怡。是夏天避暑，冬季滑雪，高山温泉的绝佳地点。酒店屋顶360度全景露台更可以观赏到壮丽酒店位于海拨1000米的山顶，拥有连绵的竹海景观，空气含有大量富氧离子，让人心旷神怡。是夏天避暑，冬季滑雪，高山酒店位于海拨1000米的山顶，拥有连绵的竹海景观，空气含有大量富氧离子，让人心旷神怡。是夏天避暑，冬季滑雪，高山酒店位于海拨1000米的山顶，拥有连绵的竹海景观(最多可写四百字）',
        rContact: 15022227541,
        rAddress: '浙江省湖州市安吉县天荒坪江南天池滑雪场傍',
        rPrice: 89,
        rLikers: 20,
        rCover: '/static/spotcover1.jpg',
        rVideo: '/static/1.1.2.mp4'
      }
    },
    created () {
      this.menun()
    },
    methods: {
      showChange (obj) {
        if (obj === 'modal-img') {
          this.showModalValue = 'modal-img'
        } else if (obj === 'modal-text') {
          this.showModalValue = 'modal-text'
        } else {
          this.showModalValue = ''
        }
      },
      showBtn () {
        this.showButtonValue = !this.showButtonValue
      },
      showBefore () {
        if (this.showImgIndex === 0) {
          this.showImgIndex = this.imgInfo.length - 1
        } else {
          this.showImgIndex -= 1
        }
      },
      showAfter () {
        if (this.showImgIndex === this.imgInfo.length - 1) {
          this.showImgIndex = 0
        } else {
          this.showImgIndex += 1
        }
      },
      ChangeGood (str, n) {
        this.good_show = str
        this.rLikers += n
      },
      menun () {
        window.scrollTo(0, 0)
      },
      scrollFun (num) {
        let n = Math.floor(num / 10) * 10
        let timer = setInterval(() => {
          /* window.alert(this.step) */
          if (this.scroll < n) {
            window.scrollTo(this.scroll, this.scroll + 10)
            this.scroll += 10
          } else {
            if (this.scroll < num) {
              window.scrollTo(this.scroll, num)
            }
            clearInterval(timer)
          }
        }, 1)
      },
      scrollUpFun () {
        this.startLocation = this.scroll
        let n = this.startLocation - Math.floor(this.startLocation / 10) * 10
        let timer = setInterval(() => {
          if (this.scroll > n) {
            window.scrollTo(this.scroll, this.scroll - 10)
            this.scroll -= 10
          } else {
            if (this.scroll > 0) {
              window.scrollTo(n, 0)
            }
            clearInterval(timer)
          }
        }, 1)
      },
      /* 需要修改 */
      menu () {
        this.scroll = document.body.scrollTop
        /* console.log('aaa' + this.scroll) */
        var btnBacktop = document.getElementsByClassName('backToTop')[0]
        if (this.scroll < 800) {
          btnBacktop.style.opacity = 0
        } else {
          btnBacktop.style.opacity = 1
        }
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
    }
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/srh.css';
@import '../../styles/model.css';
</style>



