<script>
  export default {
    data () {
      return {
        showStep: 0,
        showWay: 0,
        showLong: 0,
        showPatner: 0,
        showLike: [false, false, false, false, false, false],
        showPlace: '000',
        oneroute: [
          [{
            num: 1, placename: '田园熊出没乐园', type: '餐馆'
          }, {
            num: 2, placename: '雪莲农庄', type: '景点'
          }, {
            num: 3, placename: '欢乐风暴水上乐园', type: '景点'
          }],
          [{
            num: 4, placename: '阿忠的家青梨民宿', type: '餐馆'
          }, {
            num: 5, placename: '中国竹子博览园', type: '景点'
          }]
        ],
        showRoute: false,
        otherRoute: [
          {
            pic: 'static/lx1.png', title: '天然氧吧·安吉游'
          },
          {
            pic: 'static/lx2.png', title: '竹林深处--安吉'
          },
          {
            pic: 'static/lx3.png', title: '安吉之竹、涧、溪'
          }
        ],
        btnText: '完成定制并收藏该路线',
        scroll: 0,
        checkedNames: ['baicaoyuan','tianhuangpang','huaxuechang','shaokaochang','shaokaochang1'],
        routes: [],
        mapshow: false,
      }
    },
    created () {
      this.menun()
    },
    methods: {
      menun () {
        window.scrollTo(0, 0)
      },
      changeSelect (str, num, locate) {
        if (str === 'step') {
          this.showStep = num
          if (num === 1 && !this.showRoute) {
            console.log('还未制定路线')
          } else {
            // 定位
            let n = Math.floor(locate / 10) * 10
            let timer = setInterval(() => {
                  if (this.scroll < n) {
              window.scrollTo(this.scroll, this.scroll + 10)
              this.scroll += 10
            } else {
              if (this.scroll < locate) {
                window.scrollTo(this.scroll, locate)
              }
              clearInterval(timer)
            }
          }, 1)
          }
        } else if (str === 'way') {
          this.showWay = num
        } else if (str === 'long') {
          this.showLong = num
        } else if (str === 'patner') {
          this.showPatner = num
        } else if (str === 'like') {
          this.$set(this.showLike, num, !this.showLike[num])
        } else {
          this.showPlace = num
        }
      },
      changeOnePlace (str) {
        this.showPlace = str
        console.log(str)
      },
      showRouteFun () {
        this.showRoute = true
        this.showStep = 1
        this.handleMaps()
      },
      finishMake () {
        this.showStep = 2
        this.btnText = '已收藏'
      },
      menu () {
        this.scroll = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        // console.log(this.scroll)
        // if (this.scroll >= 216) {
        //   this.nowFixed = true
        // } else {
        //   this.nowFixed = false
        // }
      },
      handleMaps() {
        if (1) {
          console.log('执行了')
          const self = this;
          this.mapshow = true;
          const baseMap = [119.615765,30.611777]; // 出发点，定为机场或者客运中心
          // 下面是一张哈希表
          const place = {
            'tianhuangpang': {
              map: [119.615765,30.611777],
              name: '安吉山水灵峰田园熊出没乐园'
            },
            'baicaoyuan': {
              map: [119.6224195741,30.5887256557],
              name: '雪莲农庄',
            },
            'huaxuechang': {
              map: [119.595233,30.609507],
              name: '安吉欢乐风暴水上乐园'
            },
            'shaokaochang': {
              map: [119.6174882341,30.6171792990],
              name: '阿忠的家青梨民宿'
            },
            'shaokaochang1': {
              map: [119.6599684451,30.5908850414],
              name: '中国竹子博览园'
            }
          };

          const lnglat = new AMap.LngLat(...baseMap);
          const maparr = [];

          this.checkedNames.map((item, index) => {
            maparr.push(place[item]);
        });

          for(let i = 0; i < maparr.length; i++) {
            maparr[i]['distance'] =  parseInt(lnglat.distance(maparr[i].map));
            console.log(lnglat.distance(maparr[i].map))
          }

          for(let i = 0; i < maparr.length; i++) {
            for(let j = i; j < maparr.length; j++) {
              if(maparr[i]['distance'] > maparr[j]['distance']) {
                const t  = maparr[i];
                maparr[i] = maparr[j];
                maparr[j] = t;
              }
            }
          }
//
          window.map.clearMap();  // 清除地图覆盖物
          // 动态生成点
          const markers = maparr.map((item, index, arr) => {
                return {
                  icon: `http://webapi.amap.com/theme/v1.3/markers/n/mark_b${index + 1}.png`,
                  position: item.map
                }
              });
          // 添加一些分布不均的点到地图上,地图上添加三个点标记，作为参照
          markers.forEach(function (marker) {
            new AMap.Marker({
              map: window.map,
              icon: marker.icon,
              position: [marker.position[0], marker.position[1]],
              offset: new AMap.Pixel(-12, -36)
            });
          });
          // 动态生成折线图
          const lineArr = maparr.map((item, index ,arr) => {
                return item.map;
        });

          const polyline = new AMap.Polyline({
            path: lineArr,          //设置线覆盖物路径
            strokeColor: "#3366FF", //线颜色
            strokeOpacity: 1,       //线透明度
            strokeWeight: 5,        //线宽
            strokeStyle: "solid",   //线样式
            strokeDasharray: [10, 5] //补充线样式
          });
          polyline.setMap(map);

          // 导航
          const driving = new AMap.Driving();
          // 根据起终点经纬度规划驾车导航路线

          for(let i = 0; i < maparr.length - 1; i++) {
            driving.search(new AMap.LngLat(...maparr[i].map), new AMap.LngLat(...maparr[i+1].map), function (status, result) {
              self.routes.push({
                distance: parseInt(result.routes[0].distance / 10) / 100 + 'Km',
                time: parseInt(result.routes[0].time / 60) + '分钟' ,
              });
            });
          }
        }
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
      const TYPE = '美景|美食|文化|娱乐|历史';
      const NUMBER = '亲子|情侣|老人';
      const DAYS = '1|2|3|4|5|6|7';
      window.map = new AMap.Map('container', {
        resizeEnable: true,
        center: [119.63763,30.631471], // 需要改成车站的定位
        zoom: 11,
      });
    }
  }
</script>
