          {id:'8',bgImg: 'static/background/c3.jpg', name: '刘家塘村', introduction: '位于安吉县上墅乡，属于山区，海拔1465米，年平均气温15.9 ℃，年降水量1297.6毫米。'},
          {id:'5',bgImg: 'static/background/c4.jpg', name: '余村村', introduction: '村域呈东西走向，北高南低，西起东伏，群山环抱，秀竹连绵，四季常青，植被覆盖率高达96%。'},
