      changeText (num) {
        const self = this
        this.showMapText1 = false
        setTimeout(function () {
          self.showMapText1 = true
        },1)
        this.showbg1 = false
        this.showbg2 = true
        this.showXc.name = this.countryInfor[num].name
        this.showXc.id = this.countryInfor[num].id
        this.showXc.jianjie = this.countryInfor[num].introduction
        this.showNowCountryImg = this.countryInfor[num].bgImg
        this.nowCountryImgshow1 = !this.nowCountryImgshow1
        this.nowCountryImgshow2 = !this.nowCountryImgshow2
        this.showMapActive = num + ''
