        if (obj === 'modal-img') {
          this.showModalValue = 'modal-img'
        } else if (obj === 'modal-text') {
          this.showModalValue = 'modal-text'
          // this.showActIndex = index
        } else if (obj === 'modal-video') {
          this.showModalValue = 'modal-video'
          // this.showVidIndex = index
        } else {
          this.showModalValue = ''
        }
      },
      showBtn () {
        this.showButtonValue = !this.showButtonValue
      },
      showBefore () {
        if (this.showImgIndex === 0) {
          this.showImgIndex = this.imgInfo.length - 1
        } else {
          this.showImgIndex -= 1
        }
      },
      showAfter () {
        if (this.showImgIndex === this.imgInfo.length - 1) {
          this.showImgIndex = 0
        } else {
          this.showImgIndex += 1
        }
      },
      /* 监听滚动条 事件 */
      menu () {
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        const self = this
        if (this.scroll > 300) {
          this.title3Show = true
          setTimeout(function () {
            self.searchShow = true
          },500)
        }
        if (this.scroll >= 550) {
          this.showtj11 = true
          var time = 100
          setTimeout(function () {
            self.showtj21 = true
            setTimeout(function () {
              self.showtj31 = true
              setTimeout(function () {
                self.showtj41 = true
                setTimeout(function () {
