                    self.showtj22 = true
                    setTimeout(function () {
                      self.showtj42 = true
                      setTimeout(function () {
                        self.title1Show = true
                      }, time)
                    }, time)
                  }, time)
                }, time)
              }, time)
            }, time)
          }, time)
        }
        if (this.scroll > 1200) {
          this.showicon = true
          setTimeout(function () {
            self.showtext = true
            setTimeout(function () {
              self.showbtn = true
              self.title2Show = true
            }, 300)
          }, 300)
        }
      },
      /* 点定位函数（开发地图 定位点需要用到） */
      zuobiao (event) {
        /* 调用方法：@click="zuobiao($event) ，距离浏览器多高，被滚动条卷去的部分不算" */
        window.alert('Y 坐标: ' + event.clientY + ', X 坐标: ' + event.clientX)
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
    }
  };
</script>

