  .recom-context .recom-col .country-infor-contain p{
    width: 100%;
    font-size: 14px;
    box-sizing: border-box;
    padding: 10px;
    color: #666;
  }
  .recom-context .recom-col .touming{
    width: 100%;
    height: 10px;
    position: absolute;
    bottom: 0px;
    z-index: 666;
    background: -webkit-linear-gradient(rgba(239,239,239,0.2), rgba(239,239,239,0.95)); /* Safari 5.1 - 6.0 */
  }
      /* 图片集、线路、游记 */
  .recom-context .recom-col .images-belong,.route-title,.tripnotes-title{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 60;
    top: 0px;
    left: 0px;
    background: rgba(0,1,0,0.2);
  }
  .recom-context .recom-col .col-yin{
    width: 230px;
    height: 230px;
    position: absolute;
    top: 0;
    z-index: 200;
    opacity: 0;
    transition: All .5s ease-in-out;
    background: radial-gradient(rgba(255,255,255,0),rgba(0,0,0,0.3),rgba(0,0,0,0.7));
  }/* 阴影 */
  .recom-context .recom-col .col-images,.col-route,.col-tripnotes{
    width: 100%;
    cursor: pointer;
    height: 230px;
    position: relative;
  }
    .recom-context .recom-col .col-images:hover .col-yin,.col-route:hover .col-yin,.col-tripnotes:hover .col-yin{
      opacity: 1;
    }
  /* 图片路径的修改 */
  .recom-context .recom-col .topic-icon-right{
    width: 54px;
    height: 25px;
    line-height: 20px;
    padding: 0px 5px;
    display: block;
    font-size: 11px;
    float: right;
    text-align: right;
    margin-top: -30px;
    z-index: 2;
    color: #fff;
    background: url(/ssm01/static/label1.png) no-repeat 0px -59px;
  }
  .recom-context .recom-col .images-belong span,.route-title span{
    display: block;
    margin: 18px 0px 0px 23px;
    color: #fff;
  }
  .recom-context .recom-col .tripnotes-title span{
    display: block;
    margin: 180px 23px 0px 23px;
    color: #fff;
  }
  .recom-context .recom-col > div{
    box-shadow: #ccc 2px 2px 3px 2px;
  }

  /* 关于我们 */
  .aboutOur{
    min-height: 400px;
    width:1000px;
    margin: 50px auto;
  }
  .aboutOur-context{
    width: 900px;
    margin: 40px auto;
  }
  .aboutOur-col{
    /* height: 500px; */
    width: 300px;
    float: left;
    box-sizing: border-box;
    padding: 0px 25px;
  }
  .aboutOur-icon{
    height: 120px;
    width: 120px;
    margin: 0 auto;
    border: 4px solid #515151;
    border-radius: 50%;
  }
  .aboutOur-icon img{
    height: 60%;
    width: 60%;
    margin: 20%;
  }
  .aboutOur-text{
    width: 200px;
    height: 180px;
    line-height: 25px;
    font-size: 16px;
    /* background: lightblue; */
    margin: 40px auto;
    text-align: center;
  }
  .aboutOur-btn{
    width: 120px;
    height: 34px;
    border-radius: 3px;
    background: #FFDE3A;
    margin: -20px auto;
    text-align: center;
    line-height: 34px;
    font-size: 13px;
    color: #fff;
    cursor: pointer;
  }

  /* 底部 */
  .footer{
    width: 100%;
    height: 120px;
    background: #000;
    color: #fff;
    text-align: center;
    line-height: 120px;
  }

  /* 搜索模态框 */
  .des-ul{
    width: 100%;
    line-height: 25px;
    margin-bottom: 15px;
  }
  .des-li{
    float: left;
    height: 30px;
    line-height: 30px;
    margin-right: 25px;
    font-size: 16px;
    cursor: pointer;
  }
  /* 联系我们 */
  .contact_us_box{
    margin-top: 100px;
    height: 330px;
    width: 100%;
    position: relative;
    overflow: hidden;
  }
  .contact_us_box input{
    outline: none;
  }
  .contact_bgimg{
    height: auto;
    width: 100%;
  }
  .contact_shadow{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
  }
  .center_box{
    height: 100%;
    width: 900px;
    /* background: skyblue; */
    margin: 0 auto;
  }
  .contact_title{
    line-height: 120px;
    height: 100px;
    font-size: 21px;
    color: #fff;
  }
  .contact_form_left{
    width: 400px;
    height: 170px;
    float: left;
  }
  .contact_form_right{
    width: 400px;
    height: 170px;
    float: right;
  }
  .message_input{
    height: 100%;
    width: 100%;
    border:  1px solid #fff;
    border-radius: 3px;
    background: rgba(255,255,255,0.1);
    padding: 10px 12px;
    color: #fff;
    box-sizing: border-box;
  }
  .name_input,.email_input{
    width: 100%;
    border:  1px solid #fff;
    box-sizing: border-box;
    border-radius: 3px;
    background: rgba(255,255,255,0.1);
    padding: 0px 12px;
    color: #fff;
    height: 40px;
    margin-bottom: 25px;
  }
  .send_input{
    width: 100%;
    border:  1px solid #fff;
    border-radius: 3px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #fff;
    height: 40px;
    font-weight: 600;
    cursor: pointer;
    box-sizing: border-box;
  }
</style>



