<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active">我的乡村</span>
    </div>
    <div class="midr2 content">
      <!-- 添加乡村按钮 -->
      <div v-show="!showtab">
        <router-link :to="{path: '/add/xiangcunAdd'}" target="_blank">
          <div class="divNone">
            <img src="static/moren.jpg" alt="">您还没有添加乡村哟，快点击添加吧~
          </div>
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div v-show="showtab">
        <!-- 我的乡村 -->
        <div class="cellXCwarp">
          <div class="cellXC">
            <div class="cellimg">
              <img class="transition3" :src="myCountry.xcImg">
            </div>
            <div class="cellyin"></div>
            <div class="celltext transition3">
              {{myCountry.xcName}}
            </div>
            <div class="celltext2 transition3">
              <router-link :to="{path: '/show/countryside', query: { id: myCountry.xcId }}" target="_blank">
                <img src="static/search.png" title="查看">
              </router-link>
              <router-link :to="{path: '/change/xiangcunChange', query: { id: myCountry.xcId }}" target="_blank">
                <img src="static/change.png" title="修改">
              </router-link>
              <a href="javascript:;" @click.prvent="deleteXcFun(myCountry.xcId)" target="_blank">
                <img src="static/delete.png" title="删除">
              </a><!--
              <router-link :to="{path: '/change/xiangcunDelete', query: { id: myCountry.xcId }}">
                <img src="static/delete.png" title="删除">
              </router-link>-->
            </div>
          </div>
          <div class="cellXC2">
            <div class="numText">
              <div class="title">
                收藏量
              </div>
              <div class="value">
                {{xcColl2}}
              </div>
            </div>
            <div class="numImg" style="background: #FFA07A">
              <img src="static/shuju.png" alt="">
            </div>
          </div>
          <div class="cellXC2">
            <div class="numText">
              <div class="title">
                浏览量
              </div>
              <div class="value">
                {{xcLiuLan2}}
              </div>
            </div>
            <div class="numImg" style="background: #ADD8E6">
              <img src="static/shuju2.png" alt="">
            </div>
          </div>
        </div>
        <!-- 图表 -->
        <div class="myXCdiv2">
          <div class="line">
            <v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
            <div class="main"></div>
          </div>
        </div>
        <div style="clear:both"></div>
      </div>
      <!-- 测试按钮 -->
      <!--<div style="position: fixed;right: 50px;top:50px">
        <button type="button" @click="showtab=!showtab">测试：显示默认乡村页面<br>数据暂时显示不出</button>
      </div>-->
    </div>
    <div style="clear:both"></div>

    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import header from '../chart/header'
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        myCountry: {
          xcId: '-1',
          xcName: '',
          xcTime: '',
          xcImg: 'static/moren.jpg',
          xcColl: 1,
          xcLiuLan: 1
        },
        legendArr: [],
        color: [
          '#ffa07a',
          '#add8e6',
          '#20b2aa',
          '#87cefa',
          '#ffb6c1',
          '#C98769'
        ],
        myChart: {},
        name: '折线图',
        xcColl2: '0,000',
        xcLiuLan2: '0,000',
        showtab: true
      }
    },
    methods: {
      /* alert模态框1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
        this.showModalValue = 'modal-alter'
      },
      // 删除乡村
      deleteXcFun (id) {
        var res = window.confirm('确认删除本乡村吗？删了之后就不能回复咯~')
        if (res) {
          const self = this
          axios.post('country/delete', {id: id}, {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            }
          }).then(function (res) {
            console.log(res)
            if (res.data === '删除成功') {
              self.alertModal('删除成功~')
              this.showtab = false
            } else {
              self.alertModal('删除失败~')
            }
          }).catch(function (err) {
            console.log(err)
            self.alertModal('删除失败~')
          })
        } else {
          console.log('取消删除~')
        }
      },
      // 获取乡村信息
      getData () {
        if (this.userInfo.countrysideId !== ''){
          const self = this
          axios.get('country/UserCountrysides').then(function (res) {
            self.myCountry = {}
            console.log('乡村，成功获取商业用户的乡村信息')
            self.myCountry.xcId = res.data.CountryWithBLOBs[0].id
            self.myCountry.xcName = res.data.CountryWithBLOBs[0].name
            self.myCountry.xcTime = res.data.CountryWithBLOBs[0].time
            self.myCountry.xcImg = 'img/countryside/mainPic/' + res.data.CountryWithBLOBs[0].mianpic
            if (res.data.CountryWithBLOBs[0].id) {
              self.showtab = true
              self.numChange('27','xcColl2')
              self.numChange('89','xcLiuLan2')
              console.log('成功：' + self.showtab)
            } else {
              self.showtab = false
              self.numChange('27','xcColl2')
              self.numChange('89','xcLiuLan2')
              console.log('失败1：' + self.showtab)
            }
          }).catch(function (err) {
            self.numChange('27','xcColl2')
            self.numChange('89','xcLiuLan2')
            console.log('乡村，失败获取商业用户的信息')
            console.log(err)
            console.log('失败2：' + self.showtab)
            self.showtab = false
          })
        } else {
          console.log('该用户没有添加乡村')
          self.showtab = false
        }
      },
      // 图表
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach(function (data) {
          data.selected = true
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      },
      // 浏览量和收藏量的增加
      numChange (num, objStr) {
        var numI = 0
        var indexNum = 0
        var threeNum = '000'
        const self = this
        if (num <= parseInt((this[objStr]).replace(',', ''))){
          return
        }
        var timer = setInterval(function () {
          /* 获取数据的值 字符串转为数字 */
          var nowNum = parseInt((self[objStr]).replace(',', ''))
          if (numI < num) {
            indexNum = nowNum + 1
            if (indexNum >= 1000) {
              var n = indexNum - Math.floor(indexNum / 1000) * 1000
              if (n < 10) {
                threeNum = '00' + n
              } else if (n < 100) {
                threeNum = '0' + n
              } else if (n < 1000) {
                threeNum = '' + n
              }
              self[objStr] = (Math.floor(indexNum / 1000)) + ',' + threeNum
            } else {
              if (indexNum < 10) {
                threeNum = '00' + indexNum
              } else if (indexNum < 100) {
                threeNum = '0' + indexNum
              } else if (indexNum < 1000) {
                threeNum = '' + indexNum
              }
              self[objStr] = '0,' + threeNum
            }
            numI++
          }
          if (indexNum === num) {
            clearInterval(timer)
          }
        },1)
      }
    },
    components: {
      'v-header': header
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.line .main'));
      this.myChart.setOption({
        title: {
          show: false
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          show: false
        },
        toolbox: {
          show: false
        },
        color: this.color,
        calculable: true,
        xAxis: [{
          name: '',
          type: 'category',
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          nameTextStyle: {
            color: 'rgba(255, 255, 255, 0.69)'
          },
          axisLabel: {
            textStyle: {
              color: 'white'
            }
          },
          data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
        }],
        yAxis: [{
          axisLine: {
            show: false
          },
          nameLocation: 'end',
          nameGap: 20,
          nameRotate: 0,
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: ['rgba(230, 230, 230, 0.2)']
            }
          },
          axisLabel: {
            textStyle: {
              color: 'white',
              fontSize: 14
            }
          },
          name: '',
          type: 'value',
          nameTextStyle: {
            color: 'rgba(255, 255, 255, 0.69)'
          }
        }],
        series: [{
          name: '浏览量',
          type: 'line',
          stack: '总量1',
          data: [0, 5, 17, 26, 29, 34, 39]
        }, {
          name: '收藏量',
          type: 'line',
          stack: '总量2',
          data: [0, 3, 7, 18, 20, 23, 28]
        }, {
          name: '景点数',
          type: 'line',
          stack: '总量3',
          data: [0, 1, 2, 2, 2, 2, 3]
        }, {
          name: '餐馆数',
          type: 'line',
          stack: '总量4',
          data: [1, 1, 1, 2, 2, 3, 3]
        }, {
          name: '住宿数',
          type: 'line',
          stack: '总量5',
          data: [1, 2, 2, 5, 5, 6, 7]
        }]
      });
      this._init()
    },
    created () {
      console.log(this.userInfo.countrysideId)
      const self = this
      var timer = setTimeout(function () {
        if (self.userInfo.countrysideId !== 0) {
          self.getData()
          console.log('执行了getData')
          clearTimeout(timer)
        }
      }, 1000)
    },
    props: ['userInfo']
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
  @import "../../styles/move.css";
  @import "../../styles/model.css";
  .line{
    height: 400px;
    background: #aabda2;
    background-size: 100% 100%;
    clear: both;
    width: 600px;
    padding-bottom: 50px;
  }
  .main{
    height: 400px;
    background-size: 100% 100%;
    width: 100%;
    margin-top: -15px;
  }
  .cellXCwarp{
    width: 300px;
    height: 500px;
    float: left;
    margin-right:20px;
  }
  .cellXC{
    width: 300px;
    height: 200px;
    position: relative;
    margin-bottom: 20px;
    box-shadow: #ccc 1px 1px 3px 2px;
  }
  .cellXC .cellimg{
    width: 300px;
    height: 200px;
    overflow: hidden;
    position: absolute;
    text-align: center;
  }
  .cellXC .cellimg img{
    width: auto;
    height: 260px;
  }
  .cellXC .cellyin {
    width: 100%;
    height: 200px;
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.3s;
  }
  .cellXC .celltext{
    width: 300px;
    height: 50px;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    color: #fff;
    font-size: 26px;
    font-weight: bold;
    line-height: 50px;
    letter-spacing:7px;
  }
  .cellXC .celltext2{
    width: 225px;
    height: 32px;
    position: absolute;
    top:75%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    line-height: 50px;
    opacity: 0;
    transition: All 0.4s ease-in-out;
    cursor: pointer;
    display: flex;
    justify-content: center;
  }
  .cellXC .celltext2 a{
    display: block;
    float: left;
    width: 32px;
    height: 32px;
    margin: 0px 15px;
  }
  .cellXC:hover .cellyin{
    opacity: 1;
  }
  .cellXC:hover .cellimg img{
    transform: scale(0.95);
  }
  .cellXC:hover .celltext{
    transform: translateY(-50px);
  }
  .cellXC:hover .celltext2{
    transform: translate(-50%,-50px);
    opacity: 1;
  }
  .cellXC2{
    width: 300px;
    height: 80px;
    margin-bottom: 20px;
    box-shadow: #ccc 1px 1px 3px 2px;
    box-sizing: border-box;
    border-radius: 5px;
  }
  .cellXC2 .numText{
    width: 220px;
    height:80px;
    line-height:80px;
    float: left;
    text-align: left;
    box-sizing: border-box;
    padding-left: 50px;
  }
  .cellXC2 .numText .title{
    width: auto;
    height: 30px;
    line-height: 30px;
    letter-spacing: 10px;
    font-size: 10px;
  }
  .cellXC2 .numText .value{
    width: auto;
    height: 50px;
    line-height: 35px;
    color: #999999;
    font-weight: bold;
    font-size: 28px;
  }
  .cellXC2 .numImg{
    width: 60px;
    height:60px;
    line-height:60px;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    margin: 10px 20px 10px 0px;
    background: red;
    border-radius: 5px;
    box-sizing: border-box;
  }
  .myXCdiv2{
    float: left;
  }

</style>



