<script>
  import axios from 'axios'
  export default {
    data () {
      return { /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        /* ——————————————————————————————————text模态框—————————————————————————————————— */
        textModalData: {
          title: '',
          content: ''
        },
        textModalDatas: [
          {
            title: '如何添加美丽乡村',
            content: '内容尚未添加'
          },
          {
            title: '优秀的介绍案例',
            content: '内容尚未添加'
          },
          {
            title: '图片集的添加方式',
            content: '内容尚未添加'
          },
          {
            title: '怎样的乡村展示更吸引人',
            content: '内容尚未添加'
          },
          {
            title: '美丽乡村的视频',
            content: '内容尚未添加'
          },
          {
            title: '特产的介绍',
            content: '内容尚未添加'
          }
        ],
        /* ——————————————————————————————————未分类—————————————————————————————————— */
        countrytype: '生态保护',
        showStr: 'techan',
        wenhua: 'wenhua',
        huodong: '活动',
        xcName: '',
        xcIntroduction: '',
        xcCover: '',
        lishuxian: '递铺镇',
        TClength: 0,
        /* 特产的单位元素 */
        newTC: {
          imgSrc: '',
          tcName: ''
        },
        /* 特产集 是json格式的 imgSrc，tcName */
        tcList: [],
        /* 图片集 */
        imageList: [],
        /* 视频集 */
        videoList: [],
        /* 图片集二维数组的索引 [PicIndex][] */
        PicIndex: 0,
        /* 图片集二维数组的 单位元素的数组 临时存放 */
        linshiPic: [],
        /* 后台乡村 */
        dataXiangcun: {
          name: '',
          introduction: '',
          lishuxian: '',
          fengmian: '',
          techang: [],
          images: [],
          videos: [],
          wenhua: '',
          huodong: '',
          author: ''
        },
        /* 后台乡村 */
        paramXc: new window.FormData(),
        imageListStr: '',
        tcListStr: '',
        videoListStr: ''
      }
    },
    computed: {
    },
    created () {
      this.menun()
    },
    methods: {
      /* alert模态框1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
            this.showModalValue = 'modal-alter'
      },
      /* tab选项卡 */
      showTab (str) {
        this.showStr = str
        if (str === 'techan') {
          if (this.tcList.length === 1 && this.tcList[0].imgSrc === '') {
            this.TClength = 0
          } else {
            this.TClength = this.tcList.length
          }
          console.log('当前的值为TClength：' + this.TClength)
        }
      },
      /* 刷新回到顶部 */
      menun () {
        window.scrollTo(0, 0)
      },
      /* 文化的编辑框 */
      updateData1 (data) {
        this.wenhua = data
      },
      /* 活动的编辑框 */
      updateData2 (data) {
        this.huodong = data
      },
      /* 图片集的字符串 预览 */
      imageListStr1 () {
        var imageListStr = ''
        for (var z = 0; z < this.imageList.length; z++) {
          for (var j = 0; j < this.imageList[z].length; j++) {
            imageListStr = imageListStr + this.imageList[z][j]
            if ((j + 1) < this.imageList[z].length) {
              imageListStr = imageListStr + ','
            }
          }
          if ((z + 1) < this.imageList.length) {
            imageListStr += ';'
          }
        }
        this.imageListStr = imageListStr
      },
      /* 视频集的字符串 预览 */
      videoListStr1 () {
        var videoListStr = ''
        for (var k = 0; k < this.videoList.length; k++) {
          videoListStr = videoListStr + this.videoList[k]
          if ((k + 1) < this.videoList.length) {
            videoListStr += ','
          }
        }
        this.videoListStr = videoListStr
      },
      /* 特产集的字符串 预览 */
      tcListStr1 () {
        var tcListStr = ''
        tcListStr = '['
        for (var i = 0; i < this.tcList.length; i++) {
          tcListStr = tcListStr + '{"src":"' + this.tcList[i].imgSrc + '","name":"' + this.tcList[i].tcName + '"}'
          if ((i + 1) < this.tcList.length) {
            tcListStr += ','
          }
        }
        tcListStr += ']'
        this.tcListStr = tcListStr
      },
      /* 封面图片 上传 */
      fengmianFun (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          if (/^((?!image).)+/g.test(choosePic.files[i].type)) {
            this.alertModal('请选择正确的图片格式！')
            i = 99999
          } else {
            showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
            var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
            imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
            this.xcCover = imgObjPreview.src
            this.paramXc.set('mianpic', choosePic.files[0], choosePic.files[0].name)
          }
        }
      },
      /* 特产图片 上传 */
      techangFun (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          if (/^((?!image).)+/g.test(choosePic.files[i].type)) {
            this.alertModal('请选择正确的图片格式！')
            i = 99999
          } else {
            showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' title='" + index + "' />"
            var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index - this.TClength]
            imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
            this.tcList[index].imgSrc = imgObjPreview.src
          }
        }
        this.paramXc.set('techang/pic' + (this.tcList.length - 1), choosePic.files[0], choosePic.files[0].name)
        console.log('上传时候：' + (this.tcList.length - 1))
        console.log(this.paramXc.get('techang/pic' + (this.tcList.length - 1)))
      },
      /* 特产图片 添加按钮 */
      addNewTC () {
        if (this.tcList.length > 0) {
          if (this.tcList[(this.tcList.length - 1)].imgSrc === '') {
            this.alertModal('请将之前的特产填写完整，后再添加~')
          } else {
            this.tcList.push(JSON.parse(JSON.stringify(this.newTC)))
          }
        } else {
          this.tcList.push(JSON.parse(JSON.stringify(this.newTC)))
        }
      },
      /* 特产图片 取消按钮 */
      removeTC (index) {
        console.log(this.tcList.length)
        this.tcList.splice(index, 1)
        console.log(this.tcList.length)
        for (var removeTcIndex = 0; removeTcIndex < this.tcList.length; removeTcIndex++) {
          if (removeTcIndex < index) {
            /* console.log('没有替换：')
            console.log(this.paramXc.get('techang/pic' + removeTcIndex)) */
          } else {
            /* console.log('将：')
            console.log(this.paramXc.get('techang/pic' + removeTcIndex))
            console.log('替换成：')
            console.log(this.paramXc.get('techang/pic' + (removeTcIndex + 1))) */
            this.paramXc.set('techang/pic' + removeTcIndex, this.paramXc.get('techang/pic' + (removeTcIndex + 1)))
          }
        }
        this.paramXc.delete('techang/pic' + this.tcList.length)
      },
      /* 照片集 上传 */
      imagesFun () {
        var choosePic = document.getElementsByClassName('files-input-btn')[0]
        var showPic = document.getElementsByClassName('files-show')[0]
        var cLength = choosePic.files.length
        var iLength = this.imageList.length
        /* 初始化 清空 临时数组 */
        if (cLength >= 1) {
          this.linshiPic = []
        }
        for (var j = 0; j < cLength;) {
          /* 判断是否为图片格式 */
          if (/^((?!image).)+/g.test(choosePic.files[j].type)) {
            this.alertModal('请选择正确的图片格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div class='img-cell" + (j + iLength) + "' style='width: 190px;height: 240px;float:left;margin: 0px 5px 5px 0px'>" + "<img class='pic_coll" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' style='width:190px;height:190px;display: block'><button class='btn btn-sm btn-gray btn-delfun' style='height: 30px;margin-top: 10px' type='button'>删除</button>"
            this.linshiPic[j] = window.URL.createObjectURL(choosePic.files[j])
            this.paramXc.set('meijing/pic' + this.PicIndex + j, choosePic.files[j], choosePic.files[j].name)
            j = j + 1
          }
          if (j === cLength) {
            showPic.innerHTML += '<div style="width: 98%;height: 0px;border: 1px solid lightgray;clear: both;margin:10px auto 15px"></div>'
          }
        }
        /* 存储临时数组 到imageList */
        if (cLength >= 1) {
          this.imageList[this.PicIndex] = this.linshiPic
          this.PicIndex = this.PicIndex + 1
        }
      },
      /* 视频集 上传 */
      videosFun () {
        var choosePic = document.getElementsByClassName('file-video-btn')[0]
        var showPic = document.getElementsByClassName('file-video')[0]
        var cLength = choosePic.files.length
        var iLength = this.videoList.length
        /* this.alertModal('选择照片的张数：  ' + cLength + '\n图片集的长度为： ' + iLength) */
        for (var j = 0; j < cLength; j++) {
          /*  this.alertModal('j一一一一： ' + j + '\n图片集长度  ： ' + this.imageList.length + '\n当前序号：   ' + (iLength + j)) */
          if (/^((?!video).)+/g.test(choosePic.files[j].type)) {
            this.alertModal('请选择正确的视频格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div style='width: 300px;height: 250px;float: left;margin-right: 5px;margin-bottom: 5px;'><video style='display:block;width:100%;height:100%;' controls><source class='video" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' type='video/mp4'></video><button class='btn btn-sm btn-gray btn-delfun-video' type='button' style='height: 30px;margin-top: 10px;' onclick='alert(&apos;您真细心发现了它,但是该功能还有待开发~&apos;)'>删除</button></div>"
            this.videoList[iLength + j] = window.URL.createObjectURL(choosePic.files[j])
            this.paramXc.set('meijing/video' + (iLength + j), choosePic.files[j], choosePic.files[j].name)
          }
        }
      },
      /* 预览按钮 */
      yulanFun () {
        this.imageListStr1()
        this.tcListStr1()
        this.videoListStr1()
        console.log('\n\n\n\n预览发送的消息：')
        console.log('名称:' + this.xcName)
        console.log('简介:' + this.xcIntroduction)
        console.log('隶属:' + this.lishuxian)
        console.log('封面:' + this.xcCover)
        console.log('文化' + this.wenhua.length)
        console.log('活动' + this.huodong.length)
        console.log('特产集:')
        console.log(this.tcListStr)
        console.log('图片集:')
        console.log(this.imageListStr)
        console.log('视频集:')
        console.log(this.videoListStr)
        console.log('乡村类型')
        console.log(this.countrytype)
        var a = document.getElementById('ylBtn')
        a.href = '#/show/xiangcunShow2?name=' + this.xcName + '&introduction=' + this.xcIntroduction + '&lishuxian=浙江省 湖州市 安吉县  ' + this.lishuxian + ' ' + this.xcName + '&fengmian=' + this.xcCover + '&techang=' + this.tcListStr + '&images=' + this.imageListStr + '&videos=' + this.videoListStr + '&wenhua=' + this.wenhua + '&huodong=' + this.huodong + '&countrytype=' + this.countrytype
      },
      /* 后台 发送乡村的数据 */
      sendXiangcun () {
        this.paramXc.set('name', this.xcName)
        this.paramXc.set('introduce', this.xcIntroduction)
        this.paramXc.set('location', '浙江省 湖州市 安吉县 ' + this.lishuxian + ' ' + this.xcName)
        this.paramXc.set('culture', this.wenhua)
        this.paramXc.set('activities', this.huodong)
        this.paramXc.set('countrytype', this.countrytype)
        console.log('\n\n\n\n向后台-----------------------------提交发送的乡村信息如下：')
        console.log('————标题————')
        console.log(this.paramXc.get('name'))
        console.log('————简介————')
        console.log(this.paramXc.get('introduce'))
        console.log('————隶属镇————')
        console.log(this.paramXc.get('location'))
        console.log('————文化————')
        console.log(this.paramXc.get('culture').length)
        console.log('————活动————')
        console.log(this.paramXc.get('activities').length)
        console.log('————封面————')
        console.log(this.paramXc.get('mianpic'))
        console.log('————特产图片————')
        for (var tcIndexI = 0; tcIndexI < this.tcList.length; tcIndexI++) {
          console.log(this.paramXc.get('techang/pic' + tcIndexI))
        }
        console.log('————特产文字————')
        for (var i = 0; i < this.tcList.length; i++) {
          this.paramXc.set('techang/name' + i, this.tcList[i].tcName)
          console.log(this.paramXc.get('techang/name' + i))
        }
        console.log('————图片集————')
        for (var picI = 0; picI < this.imageList.length; picI++) {
          for (var picJ = 0; picJ < this.imageList[picI].length; picJ++) {
            console.log(this.paramXc.get('meijing/pic' + picI + picJ).name)
          }
          console.log('------------')
        }
        console.log('————视频集————')
        console.log(this.videoList)
        for (var videoI = 0; videoI < this.videoList.length; videoI++) {
//          console.log(this.paramXc.get('meijing/video' + videoI))
          console.log(this.paramXc.get('meijing/video' + videoI).name)
        }
        console.log('————乡村类型————')
        console.log(this.paramXc.get('countrytype'))
        const self = this
        axios.post('country/newCountry', self.paramXc, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
          }
        }).then(function (res) {
          if (res.data === 'success') {
            this.alertModal('添加成功')
          } else {
            this.alertModal('添加失败')
          }
        }).catch(function (err) {
          console.log('添加失败' + err)
        })
      },
      /* text模态框1 */
      closeTextModal () {
        this.showModalValue = ''
      },
      /* text模态框1 */
      alertTextModal (index) {
        this.showModalValue = 'modal-text'
        this.textModalData = this.textModalDatas[index]
      },
      /* 尚未开启的功能 */
      sorry () {
        this.alertModal('您真细心发现了它,但是该功能还有待开发')
      }
    }
  }
</script>


