        for (var i = 0; i < this.tcList.length; i++) {
          tcListStr = tcListStr + '{"src":"' + this.tcList[i].imgSrc + '","name":"' + this.tcList[i].tcName + '"}'
          if ((i + 1) < this.tcList.length) {
            tcListStr += ','
          }
        }
        tcListStr += ']'
        this.tcListStr = tcListStr
      },
      /* 照片集 上传 */
      imagesFun () {
        var choosePic = document.getElementsByClassName('files-input-btn')[0]
        var showPic = document.getElementsByClassName('files-show')[0]
        var cLength = choosePic.files.length
        var iLength = this.imageList.length
        /* 初始化 清空 临时数组 */
        if (cLength >= 1) {
          this.linshiPic = []
        }
        for (var j = 0; j < cLength;) {
          /* 判断是否为图片格式 */
          if (/^((?!image).)+/g.test(choosePic.files[j].type)) {
            window.alert('请选择正确的图片格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div class='img-cell" + (j + iLength) + "' style='width: 190px;height: 240px;float:left;margin: 0px 5px 5px 0px'>" + "<img class='pic_coll" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' style='width:190px;height:190px;display: block'><button class='btn btn-sm btn-gray btn-delfun' style='height: 30px;margin-top: 10px' type='button'>删除</button>"
            this.linshiPic[j] = window.URL.createObjectURL(choosePic.files[j])
            this.paramXc.set('meijing/pic' + this.PicIndex + j, choosePic.files[j], choosePic.files[j].name)
            j = j + 1
          }
          if (j === cLength) {
            showPic.innerHTML += '<div style="width: 98%;height: 0px;border: 1px solid lightgray;clear: both;margin:10px auto 15px"></div>'
          }
        }
        /* 存储临时数组 到imageList */
        if (cLength >= 1) {
          this.imageList[this.PicIndex] = this.linshiPic
          this.PicIndex = this.PicIndex + 1
        }
      },
      /* 视频集 上传 */
      videosFun () {
        var choosePic = document.getElementsByClassName('file-video-btn')[0]
        var showPic = document.getElementsByClassName('file-video')[0]
        var cLength = choosePic.files.length
        var iLength = this.videoList.length
        /* window.alert('选择照片的张数：  ' + cLength + '\n图片集的长度为： ' + iLength) */
        for (var j = 0; j < cLength; j++) {
          /*  window.alert('j一一一一： ' + j + '\n图片集长度  ： ' + this.imageList.length + '\n当前序号：   ' + (iLength + j)) */
          if (/^((?!video).)+/g.test(choosePic.files[j].type)) {
            window.alert('请选择正确的视频格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div style='width: 300px;height: 250px;float: left;margin-right: 5px;margin-bottom: 5px;'><video style='display:block;width:100%;height:100%;' controls><source class='video" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' type='video/mp4'></video><button class='btn btn-sm btn-gray btn-delfun-video' type='button' style='height: 30px;margin-top: 10px;'>删除</button></div>"
            this.videoList[iLength + j] = window.URL.createObjectURL(choosePic.files[j])
            this.paramXc.set('meijing/video' + j, choosePic.files[j], choosePic.files[j].name)
          }
        }
      },
      /* 封面图片 上传 */
      fengmianFun (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.xcCover = imgObjPreview.src
          this.paramXc.set('mianpic', choosePic.files[0], choosePic.files[0].name)
        }
      },
      /* 特产图片 上传 */
      techangFun (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' title='" + index + "' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index - this.TClength]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.tcList[index].imgSrc = imgObjPreview.src
        }
        this.paramXc.set('techang/pic' + (this.tcList.length - 1), choosePic.files[0], choosePic.files[0].name)
        console.log('上传时候：' + (this.tcList.length - 1))
        console.log(this.paramXc.get('techang/pic' + (this.tcList.length - 1)))
      },
      /* 特产图片 添加按钮 */
      addNewTC () {
        if (this.tcList.length > 0) {
          if (this.tcList[(this.tcList.length - 1)].imgSrc === '') {
            window.alert('请将之前的特产填写完整~')
          } else {
            this.tcList.push(JSON.parse(JSON.stringify(this.newTC)))
          }
        } else {
          this.tcList.push(JSON.parse(JSON.stringify(this.newTC)))
        }
      },
      /* 特产图片 取消按钮 */
      removeTC (index) {
        console.log(this.tcList.length)
        this.tcList.splice(index, 1)
        console.log(this.tcList.length)
        for (var removeTcIndex = 0; removeTcIndex < this.tcList.length; removeTcIndex++) {
          if (removeTcIndex < index) {
            /* console.log('没有替换：')
             console.log(this.paramXc.get('techang/pic' + removeTcIndex)) */
          } else {
            /* console.log('将：')
             console.log(this.paramXc.get('techang/pic' + removeTcIndex))
             console.log('替换成：')
             console.log(this.paramXc.get('techang/pic' + (removeTcIndex + 1))) */
            this.paramXc.set('techang/pic' + removeTcIndex, this.paramXc.get('techang/pic' + (removeTcIndex + 1)))
          }
        }
        this.paramXc.delete('techang/pic' + this.tcList.length)
      },
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      }
    }
  }
</script>


