<template>
  <div>
    <div class="headbar">
    </div>
    <div class="bodybar">
      <div class="top fitimgwidth">
        <img :src="'img/articles/cover/' + yj.yjImg">
        <div class="overwrap"></div>
        <div class="p">
          <div class="title">·<span v-text="yj.yjName"></span></div>
          <div class="author" v-text="yj.yjAuthor"></div>
          <div class="time" v-text="yj.yjTime"></div>
        </div>
      </div>
      <div contenteditable="false" class="content">
        <vue-html5-editor contenteditable="false" :content="content" :height="500" :z-index="1000" :auto-height="true"></vue-html5-editor>
      </div>
      <div class="like">
        <a href="javascript:;" @click=" showa ('likeA',1) " v-if=" show === 'like' ">
          <img src="static/like.png">
        </a>
        <a href="javascript:;" @click=" showa ('like',-1) " v-if=" show === 'likeA' ">
          <img src="static/likeA.png">
        </a>
        <span v-text="yj.yjLikeNum"></span>
      </div>
    </div>
    <!-- 评论 -->
    <div class="onebar">
      <div class="hengg1 clear"></div>
      <!-- 评分处 -->
      <div class="stars">
        <ul>
          <li>评分：</li>
          <li @click=" showStar (1) " v-if=" 1 <= starNum ">
            <img src="static/icons/flo_s.png"></li>
          <li @click=" showStar (2) " v-if=" 2 <= starNum ">
            <img src="static/icons/flo_s.png"></li>
          <li @click=" showStar (3) " v-if=" 3 <= starNum ">
            <img src="static/icons/flo_s.png"></li>
          <li @click=" showStar (4) " v-if=" 4 <= starNum ">
            <img src="static/icons/flo_s.png"></li>
          <li @click=" showStar (5) " v-if=" 5 <= starNum ">
            <img src="static/icons/flo_s.png"></li>
          <li @click=" showStar (1) " v-if=" 1 > starNum ">
            <img src="static/icons/flo_k.png"></li>
          <li @click=" showStar (2) " v-if=" 2 > starNum ">
            <img src="static/icons/flo_k.png"></li>
          <li @click=" showStar (3) " v-if=" 3 > starNum ">
            <img src="static/icons/flo_k.png"></li>
          <li @click=" showStar (4) " v-if=" 4 > starNum ">
            <img src="static/icons/flo_k.png"></li>
          <li @click=" showStar (5) " v-if=" 5 > starNum ">
            <img src="static/icons/flo_k.png"></li>
        </ul>
        <div>
          <span>{{starNum}}.0</span>分</div>
      </div>
      <!-- 评论处 -->
      <form>
        <textarea class="remark_textarea" rows="13" placeholder="我有独特的见解..." v-model="commentText"></textarea>
        <input type="submit" class="clear remark_sub" value="提交" @click.prevent="addComment">
      </form>
      <!-- 用户的评论 -->
      <div class="re_notice" v-if="comment.length == 0">
        暂无评论，我来发表第一篇评论！
      </div>
      <div class="remark_con clear">
        <div class="one_rem clear" v-for="(item,index) in comment" v-bind:index="index">
          <div class="remark_infor">
            <div class="user_hp">
              <img :src="item.headp">
            </div>
            <div class="na_time">
              <div class="user_na">{{item.name}}</div>
              <div class="time_re">{{item.time}}</div>
            </div>
            <div class="like_dis">
              <ul>
                <li v-if="item.like_show === 'like'" @click="ChangeLike(item,'liked',1)"><img src="static/icons/like.png"></li>
                <li v-if="item.like_show === 'liked'" @click="ChangeLike(item,'like',-1)"><img src="static/icons/liked.png"></li>
                <li>{{item.like}}</li>
                <li v-if="item.dislike_show === 'dislike'" @click="ChangeDislike(item,'disliked',1)"><img src="static/icons/dislike.png"></li>
                <li v-if="item.dislike_show === 'disliked'" @click="ChangeDislike(item,'dislike',-1)"><img src="static/icons/disliked.png"></li>
                <li>{{item.dislike}}</li>
              </ul>
            </div>
          </div>
          <div class="remark_text clear">
            <div class="or_con">{{item.content}}</div>
            <div class="hengg2"></div>
            <div class="or_score clear">
              <ul>
                <li v-if=" 1 <= item.scores ">
                  <img src="static/icons/flo_s.png"></li>
                <li v-if=" 2 <= item.scores ">
                  <img src="static/icons/flo_s.png"></li>
                <li v-if=" 3 <= item.scores ">
                  <img src="static/icons/flo_s.png"></li>
                <li v-if=" 4 <= item.scores ">
                  <img src="static/icons/flo_s.png"></li>
                <li v-if=" 5 <= item.scores ">
                  <img src="static/icons/flo_s.png"></li>
                <li v-if=" 1 > item.scores ">
                  <img src="static/icons/flo_k.png"></li>
                <li v-if=" 2 > item.scores ">
                  <img src="static/icons/flo_k.png"></li>
                <li v-if=" 3 > item.scores ">
                  <img src="static/icons/flo_k.png"></li>
                <li v-if=" 4 > item.scores ">
                  <img src="static/icons/flo_k.png"></li>
                <li v-if=" 5 > item.scores ">
                  <img src="static/icons/flo_k.png"></li>
              </ul>
              <span style="color:#7b7b7b;">{{item.scores}}.0 分</span>
            </div>
          </div>
        </div>
      </div>
      <!-- 分页 -->
      <div class="pages" v-if="comment.length >= 5">
        <ul>
          <li style="color: #FFDE3A;border-color: #FFDE3A;">1</li>
          <li>2</li>
          <li>></li>
        </ul>
      </div>
    </div>
    <div class="footbar">
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'like',
        yj: {
          yjLikeNum: 0,
          yjid: 1,
          yjName: '故地重游 余村1',
          yjAuthor: '阿水12344',
          yjTime: '2017/7/2',
          yjImg: '',
          yjCon: ''
        },
        shuju: '',
        content: '',
        comment: [],
        commentText: '',
        starNum: 0
      }
    },
    /* 游记通过传入所有参数显示 */
    created () {
      this.menun()
      this.shuju = this.GetQueryString('id')
      this.getYOUji()
      this.displayNone()
    },
    methods: {
      showa (str, num) {
        this.show = str
        this.yj.yjLikeNum += num
      },
      /* 获取游记的信息 */
      getYOUji () {
        const self = this
        axios.post('userarticle/getOneArticleById', self.shuju, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }).then(function (res) {
          console.log('游记，获取成功：')
          console.log(res)
          self.yj.yjAuthor = res.data.username
          self.yj.yjid = res.data.idarticle
          self.yj.yjImg = res.data.mainpic
          self.yj.yjName = res.data.name
          self.yj.yjTime = self.dateFormat(res.data.articletime)
          self.content = res.data.content
        }).catch(function (err) {
          console.log('游记，获取游记失败！' + err)
        })
      },
      menun () {
        window.scrollTo(0, 0)
      },
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      /* 去除vue-html5-editor的样式 */
      displayNone () {
        setTimeout(
          function () {
            var toolbar = document.getElementsByClassName('toolbar')[0]
            var editor = document.getElementsByClassName('vue-html5-editor')[0]
            toolbar.style.display = 'none'
            editor.style.border = '0px solid white'
            console.log('执行了displayNone')
            var contentdiv = document.getElementsByClassName('content')
            for (var icontent = 0; icontent < contentdiv.length; icontent++) {
              contentdiv[icontent].contentEditable = 'false'
            }
          }, 30
        )
      },
      /* 参数vue-html5-editor内容的格式化(暂时不需要) */
      replaceFun2 (str) {
        if (str !== null) {
          return str.replace(/%3D/g, '=').replace(/%3B/g, ';').replace(/%3A/g, ':').replace(/%2F/g, '/')
        } else {
          return null
        }
      },
      /* 参数封面的格式化 */
      replaceFun1 (str) {
        if (str !== null) {
          return str.replace(/%3A/g, ':').replace(/%2F/g, '/')
        } else {
          return null
        }
      },
      /* 添加留言（前端的效果）（需要修改）（将其加入到 添加成功下面 并需要后台返回 当前用户的姓名） */
      addComment () {
        this.comment.push({
          name: this.message.userName,
          headp: 'img/headpic/' + this.message.userPic,
          time: getTime(),
          content: this.commentText,
          like_show: 'like',
          dislike_show: 'dislike',
          like: 0,
          dislike: 0,
          scores: this.starNum
        })
        this.commentText = ''
        this.starNum = 0
      },
      /* 添加留言 */
      showStar (num) {
        this.starNum = num
      },
      ChangeLike (item, str, n) {
        item.like_show = str
        item.like += n
      },
      ChangeDislike (item, str, n) {
        item.dislike_show = str
        item.dislike += n
      },
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      }
    },
    props: ['message']
  }
  function getTime () {
    var now = new Date()
    var year = now.getFullYear()
    var month = now.getMonth() + 1
    var day = now.getDate()
    month = parseInt(month) < 10 ? '0' + month : month
    day = parseInt(day) < 10 ? '0' + day : day
    return year + '-' + month + '-' + day
  }
</script>

<style scoped>
  @import '../../styles/style.css';
  @import '../../styles/remark.css';
  .re_notice{
    width: 100%;
    height:100px;
    color: gray;
    text-align: center;
  }
  .onebar{
    width: 1100px;
    height: auto;
    clear: both;
    border-bottom: 2px solid #ccc;
    margin: 0px auto;
  }
  .top{
    width:100%;
    height:490px;
    position: relative;
  }
  .top>img{
    /*width: 100%;*/
    /*height: 100%;*/
    position: absolute;
  }
  .top .overwrap{
    width:100%;
    height:100%;
    position: absolute;
    background: rgba(48,48,48,0.45);
  }
  .top .p{
    min-width: 40%;
    height: 50%;
    right: 0px;
    top:50%;
    transform: translateY(-50%);
    position: absolute;
    padding-right: 150px;
    text-align: right;
  }
  .top .p .title{
    font-size: 52px;
    font-weight: bold;
    color: #ffffff;
    height:60%;
    line-height: 4;
  }
  .top .p .author{
    color: #ffffff;
    font-weight: bold;
    font-size: 22px;
    height: 20%;
  }
  .top .p .time{
    color: #aaa;
    height:20%;
    font-size: 22px;
  }
  .top .next-btn{
    width: 100%;
    height: auto;
    position: absolute;
    text-align: center;
    bottom: 0;
  }
  .top .next-btn img{
    width: 110px;
    height: 60px;
  }
  .content{
    width: 1100px;
    height: auto;
    margin:0 auto;
    min-height: 550px;
    padding: 50px 100px;
    box-sizing: border-box;
  }
  .content img{
    width: 80%;
    height: auto;
    margin: 20px 10%;
  }
  .content p{
    line-height: 2;
  }
  .like{
    width: 80px;
    height:40px;
    line-height: 40px;
    margin:50px auto;
    text-align: center;
  }
  .like span{
    height:40px;
    line-height: 40px;
    width: 40px;
    display: block;
    float: left;
  }
  .like a img{
    height:40px;
    width: 40px;
    line-height: 40px;
  }
  .like a{
    width: 40px;
    height: 40px;
    display: block;
    float: left;
  }
</style>



