<script>
	// 滚动显示相应的天数，路线信息的data形式
	export default {
		data () {
			return {
				showDay: 0,
				scroll: 0,
				nowFixed: false
			}
		},
		created () {
			this.menun()
		},
		methods: {
			menun () {
				window.scrollTo(0, 0)
			},
			// 定位以及改样式函数
			locatedDay (num1, num2) {
				// 改样式
				this.showDay = num1
				// 获取这一天内容的高度
				var divBox = document.getElementsByClassName('which-day')[num1]
				var nowDivTop = divBox.offsetTop - 80
				console.log('局顶部' + nowDivTop)
				// 定位
				let n = Math.floor(nowDivTop / 10) * 10
				let timer = setInterval(() => {
							if (this.scroll < n) {
					window.scrollTo(this.scroll, this.scroll + 10)
					this.scroll += 10
				} else {
					if (this.scroll < nowDivTop) {
						window.scrollTo(this.scroll, nowDivTop)
					}
					clearInterval(timer)
				}
			}, 1)
			},
			menu () {
				this.scroll = document.documentElement.scrollTop || document.body.scrollTop
				console.log(this.scroll)
				if (this.scroll >= 216) {
					this.nowFixed = true
				} else {
					this.nowFixed = false
				}
			}
			// 获取标题到当前窗口顶部的距离
			// testFun () {
			//   var titleDiv = document.getElementsByClassName('route-title')[0]
			//   console.log(titleDiv)
			//   var nowTopDis = titleDiv.offsetTop
			//   console.log(nowTopDis)
			//   let timer = setInterval(() => {
			//     if (nowTopDis > 0) {
			//       nowTopDis = titleDiv.offsetTop
			//       console.log(nowTopDis)
			//       console.log(111)
			//     } else {
			//       clearInterval(timer)
			//       console.log(222)
			//       console.log(nowTopDis)
			//     }
			//   }, 1)
			// }
		},
		mounted () {
			window.addEventListener('scroll', this.menu)
		}
	}
</script>
