            return 'red'
          } else if (event === 'chakanSrc') {
            return 'javascript:;'
            /* 触发模态框 */
          } else if (event === 'chakanText') {
            return '查看原因'
          } else if (event === 'xiugaiText') {
            return '修改重交'
          }
        } else {
          if (event === 'color') {
            return 'gray'
          } else if (event === 'chakanSrc') {
            return 'javascript:;'
            /* 触发模态框 */
          } else if (event === 'chakanText') {
