import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '../pages/common/404.vue'
import { Main, ShowIndex, Personal, DisplayRemarkable, ShowLAR, AddIndex, PersonalO, ChangeIndex, Travel } from './routes'
Vue.use(Router)

const root = Vue.component('root', {
  template: '<router-view></router-view>'
})

export default new Router({
  routes: [
    {
      path: '/404',
      name: '404',
      component: NotFound
    },
    {
      path: '/',
      name: 'XX系统',
      component: root,
      children: [
        Main,
        ShowIndex,
        Personal,
        DisplayRemarkable,
        ShowLAR,
        AddIndex,
        PersonalO,
        ChangeIndex,
        Travel
      ]
    },
    {
      path: '*',
      redirect: {path: '/404'}
    }
  ]
})



