<template>
  <div class="warp">
    <!-- 筛选&搜索 -->
    <div class="topbar">
      <div class="topimg imgcenter">
        <img src="static/view.jpg" alt="">
      </div>
      <div class="topyin"></div>
      <div class="search">
        <input type="text" placeholder="全部乡村" v-model="searchXc_value">
        <button type="button" @click="select3"><img src="static/search.png" alt=""></button>
      </div>
      <div class="selectbar">
        <div class="noselect">
          <ul>
            <span class="ultitle">类型:</span>
            <li :class="{liactive: selectbar1 === '全部'}" @click="select1('全部')">全部</li>
            <li :class="{liactive: selectbar1 === '文化传承'}" @click="select1('文化传承')">文化传承</li>
            <li :class="{liactive: selectbar1 === '生态保护'}" @click="select1('生态保护')">生态保护</li>
            <li :class="{liactive: selectbar1 === '产业发展'}" @click="select1('产业发展')">产业发展</li>
          </ul>
          <ul>
            <span class="ultitle">筛选:</span>
            <li :class="{liactive: selectbar2 === '全部'}" @click="select2('全部')">全部</li>
            <li :class="{liactive: selectbar2 === '热门'}" @click="select2('热门')">热门</li>
            <li :class="{liactive: selectbar2 === '最新'}" @click="select2('最新')">最新</li>
            <li :class="{liactive: selectbar2 === '推荐'}" @click="select2('推荐')">推荐</li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 加载内容 -->
    <div class="bodybar" id="bodybar">
      <div class="body2 clear">
        <transition-group name="slide-fade">
          <div class="cell" v-for="(xc, index) in country" :key="index">
            <div class="cellimg">
              <img class="transition3" :src="'img/countryside/mainPic/' + xc.mianpic">
            </div>
            <div class="cellyin"></div>
            <div class="celltext transition3">
              {{xc.name}}
            </div>
            <div class="celltext2 transition3">
              <router-link :to="{path: '/show/countryside', query: { id: xc.id }}" target="_blank">
                <img src="static/search.png" alt="">
              </router-link>
            </div>
            <div class="celltext3">
              <span class="biaoqian">{{xc.countrytype}}</span>
            </div>
          </div>
        </transition-group>
      </div>
      <!-- 加载状态 -->
      <div class="waitbar" v-show="showWait">
        <img src="static/wait.gif">
      </div>
      <div class="waitbar" v-show="!showWait">
        {{showWaitText}}
      </div>
    </div>
    <!-- 回到顶部 -->
    <div class="backToTop transition1 rotateY" @click="scrollUpFun()">
      <img src="static/toup2.png">
    </div>
    <!-- 滚动测试数据 -->
    <!--<div style="position: fixed;top: 50px;left: 50px;width: 50px;height: 100px;background: white">
      {{scroll}}<br>
      {{getXcIndex}}<br>
      {{scrollIndex}}<br>
      {{country.length}}<br>
    </div>-->
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————其他—————————————————————————————————— */
        // 加载乡村函数的 调用 参数（滚轮每滚600px）
        scrollIndex: 600,
        // 当前滚轮位置
        scroll: 0,
        // 执行getXc
        doGetXc: true,
        /* ——————————————————————————————————动画—————————————————————————————————— */
        // 等待加载或加载完全 的显示
        showWait: false,
        /* ——————————————————————————————————当前显示—————————————————————————————————— */
        // 筛选
        selectbar1: '全部',
        selectbar2: '全部',
        searchXc_value: '',
        // 加载乡村个数*6
        getXcIndex: 0,
        showWaitText: '已全部加载完~~~',
        /* ——————————————————————————————————前台—————————————————————————————————— */
        // 乡村的数据
        country: [],
        /* ——————————————————————————————————后台—————————————————————————————————— */
        // 筛选 传给后台的参数
        sxValue: {
          type1: '',
          type2: '',
          page: 0,
          key: ''
        }
        /* ——————————————————————————————————模态框—————————————————————————————————— */
      }
    },
    methods: {
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      // 获取筛选之后的信息
      getXc () {
        this.doGetXc = false
        // 定义需要发送给后台的数据
        this.sxValue.type1 = this.selectbar1
        this.sxValue.type2 = this.selectbar2
        this.sxValue.page = this.getXcIndex
        this.sxValue.key = this.searchXc_value
        console.log('全部乡村,执行了筛选乡村函数，发送给后台的参数：')
        console.log(this.sxValue)
        this.showWait = true
        const self = this
        axios.post('country/ByType', self.sxValue, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }).then(function (res) {
          console.log('发送筛选信息成功，后台返回：')
          console.log(res)
          // 当获取第一页数据 初始化county为空
          if (self.getXcIndex === 0) {
            self.country = []
            self.showWaitText = '已全部加载完~~~'
          }
          // 当获取第一页数据 为空
          if (res.data.length === 0 && self.getXcIndex === 0) {
            self.country = []
            self.scrollIndex = 2000
            self.showWaitText = '没有相关数据~~~'
            console.log('第一次乡村筛选数据为空')
            self.getXcIndex++
          } else if (res.data.length === 0){
            // 单页返回为空
            self.scrollIndex = 2000
            console.log(self.getXcIndex + '，此次乡村筛选数据为空')
            self.getXcIndex++
          } else if (res.data.length < 6){
            // 单页返回的长度小于6 对country追加赋值
            self.scrollIndex = 2000
            console.log(self.getXcIndex + '，此次乡村筛选数据少于6条')
            self.getXcIndex++
            self.country = self.country.concat(res.data)
          } else {
            // 单页返回6条 对country追加赋值
            console.log(self.getXcIndex + '乡村筛选数据为6条')
            self.getXcIndex++
            self.country = self.country.concat(res.data)
          }
          self.showWait = false
          self.doGetXc = true
        }).catch(function (err) {
          console.log('发送筛选信息失败！' + err)
          self.scrollIndex = 2000
          self.getXcIndex++
          self.showWait = false
          self.showWaitText = '没有相关数据~~~'
          self.doGetXc = true
        })
      },
      // 乡村类型筛选1
      select1 (str) {
        this.selectbar1 = str
        this.getXcIndex = 0
        this.scrollIndex = 600
        this.country = []
        this.getXc()
      },
      // 乡村相关筛选2
      select2 (str) {
        this.selectbar2 = str
        this.getXcIndex = 0
        this.scrollIndex = 600
        this.country = []
        this.getXc()
      },
      // 搜索框3
      select3 () {
        this.getXcIndex = 0
        this.scrollIndex = 600
        this.country = []
        this.getXc()
      },
      // 滚轮监听事件
      menu () {
        // 加载乡村数据 （每滚动scrollIndex像素）
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        if (this.doGetXc) {
          console.log('滚轮：' + this.scroll + ' ：' + this.getXcIndex*this.scrollIndex + ',value:' + this.doGetXc)
          if (this.scroll >= (this.getXcIndex * this.scrollIndex) && this.getXcIndex > 0) {
            this.getXc()
          }
        }
        /* 回到顶部按钮 */
        var btnBacktop = document.getElementsByClassName('backToTop')[0]
        if (this.scroll < 500) {
          btnBacktop.style.opacity = '0'
        } else {
          btnBacktop.style.opacity = '1'
        }
      },
      // 回到顶部
      scrollUpFun () {
        console.log('执行了回到顶部函数')
        const self = this
        var n = this.scroll - Math.floor(this.scroll / 10) * 10
        var timer = setInterval(function () {
          if (self.scroll > n) {
            window.scrollTo(self.scroll, self.scroll - 10)
            self.scroll -= 10
          } else {
            if (self.scroll > 0) {
              window.scrollTo(n, 0)
            }
            clearInterval(timer)
          }
        }, 1)
      }
    },
    created () {
      this.getXc()
      if (this.GetQueryString('key')) {
        this.key = decodeURI(this.GetQueryString('key'))
        this.searchXc_value = decodeURI(this.GetQueryString('key'))
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
    }
  }
</script>

<style scoped lang="stylus">
  @import "../../styles/style.css";
  @import "../../styles/move.css";
  .warp{
    width: 100%;
    height: auto;
    background: #f5f5f5;
  }
  .topbar{
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;
  }
  .topimg{
    overflow: hidden;
    width: 100%;
    position: absolute;
  }
  .topbar .topimg img {
    width: 100%;
  }
  .topyin{
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0,0,0,0.5);
  }
  .search{
    width: 430px;
    height: 50px;
    position: absolute;
    border: 2px solid #FFDE3A;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .search input{
    background: transparent;
    border: 0px solid white;
    height: 50px;
    line-height: 50px;
    width: 380px;
    box-sizing: border-box;
    margin: 0px;
    padding: 0px 0px 0px 15px;
    display: block;
    float: left;
    outline: none;
    color: white;
  }
  .search button{
    width: 50px;
    height: 50px;
    background: #FFDE3A;
    border: 0px solid white;
    box-sizing: border-box;
    display: block;
    float: left;
    outline:none;
  }
  .selectbar{
    height: 100px;
    position: absolute;
    left: 50%;
    top: 75%;
    transform: translate(-50%,-50%);
  }
  .selectbar ul{
       width: auto;
       height: 50px;
       line-height: 50px;
       padding: 0px 0px;
       clear: both;
     }
  .selectbar ul li{
    width: auto;
    height: 30px;
    line-height: 30px;
    padding: 0px 5px;
    margin: 10px 5px;
    float: left;
    cursor: pointer;
    color: #999;
  }
  .selectbar ul .liactive{
    border-bottom: 2px solid #FFDE3A;
    color: #fff;
  }
  .selectbar ul .ultitle{
    width: 60px;
    height: 30px;
    line-height: 30px;
    margin: 10px 0px;
    text-align-last: justify;
    padding-right: 10px;
    /*border-right: 2px solid #999;*/
    display: block;
    float: left;
    color: #999;
  }
  .bodybar{
    width: 100%;
    height: auto;
    padding-bottom: 75px;
  }
  .bodybar .body2{
    width: 1100px;
    height: auto;
    min-height: 200px;
    margin: 10px auto 0px;
    clear: both;
    padding: 30px 40px;
    /*border-top: 2px solid #cccccc;*/
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
  }
  .bodybar .body2 .cell{
    width: 300px;
    height: 290px;
    float: left;
    margin: 20px;
    position: relative;
    box-shadow: #ccc 1px 1px 3px 2px;
  }
  .bodybar .body2 .cell .cellimg{
    width: 300px;
    height: 250px;
    overflow: hidden;
    position: absolute;
  }
  .bodybar .body2 .cell .cellimg img{
    width: auto;
    height: 120%;
    margin-left: -25px;
    margin-top: -25px;
  }
  .bodybar .body2 .cell .cellyin {
    width: 100%;
    height: 250px;
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.3s;
  }
  .bodybar .body2 .cell .celltext{
    width: 300px;
    height: 50px;
    position: absolute;
    text-align: center;
    top: 40%;
    transform: translateY(-50%);
    color: #fff;
    font-size: 26px;
    font-weight: bold;
    line-height: 50px;
    letter-spacing:7px;
  }
  .bodybar .body2 .cell .celltext2{
    width: 32px;
    height: 32px;
    position: absolute;
    top:58%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    line-height: 50px;
    opacity: 0;
    transition: All 0.4s ease-in-out;
    cursor: pointer;
  }
  .bodybar .body2 .cell .celltext3{
    width: 100%;
    height: 40px;
    line-height: 40px;
    color: #666;
    position: absolute;
    bottom: 0px;
    left: 0px;
    cursor: pointer;
    background: #fff;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .bodybar .waitbar{
    width: 100%;
    height: 75px;
    line-height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #999;
  }
  .bodybar .waitbar img{
    width: 100px;
    height: 20px;
  }
  .biaoqian{
    font-size: small;
    width: auto;
    display: block;
    padding: 0px 5px;
  }
  .bodybar .body2 .cell:hover .cellyin{
    opacity: 1;
  }
  .bodybar .body2 .cell:hover .cellimg img{
    transform: scale(0.95);
  }
  .bodybar .body2 .cell:hover .celltext{
    transform: translateY(-50px);
  }
  .bodybar .body2 .cell:hover .celltext2{
    transform: translate(-50%,-50px);
    opacity: 1;
  }

  .backToTop{
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 150px;
    right: 40px;
    opacity: 0;
  }
  .backToTop img{
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
</style>



