<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active">我的住宿</span>
    </div>
    <div class="midr2 content">
      <div v-if="myZhusu.length === 0">
        <router-link :to="{path: '/add/jingdianAdd', query:{type: 'hotle'}}" target="_blank">
          <div class="divNone">
            <img src="static/moren.jpg" alt="">您还没有添加住宿哟，快点击添加吧~
          </div>
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div v-if="myZhusu.length > 0">
        <router-link class="btn btn1" :to="{path: '/add/jingdianAdd', query:{type: 'hotle'}}" target="_blank">
          添加住宿
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myZs, index) in myZhusu">
        <div style="width: 300px;float: left;" class="imgcenter">
          <img :src="myZs.zsImg">
        </div>
        <div class="cellr">
          <router-link :to="{path: '/displayremark/sight', query: { id: myZs.zsId, type: 'hotle' }}" target="_blank">
            <h4 v-text="myZs.zsName"></h4>
          </router-link>
          <p class="">{{myZs.location}}</p>
          <p class="" v-text="myZs.zsTime"></p>
          <p>
            <router-link class="btn btn-sm" :to="{path: '/add/jingdianAdd', query: { id: myZs.zsId, type: 'hotle' , type2: 'change'}}" target="_blank">
              修改
            </router-link>
            <a href="javascript:;" class="btn btn-sm btn-gray" @click="deleteSandian(myZs.zsId)" style="margin-left: 20px">
              删除
            </a>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
    <!-- confirm -->
    <div class="modal" v-if="showModalValue === 'modal-confirm'" style="z-index: 2000">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close" style="width: 50%;float: left;">
            <a href="javascript:;" class="link-btn" @click="closeBtn('quxiao')" style="color: #cccccc">取消</a>
          </button>
          <button class="alter-btn-close" style="width: 50%;float: left">
            <a href="javascript:;" class="link-btn" @click="closeBtn('queren')">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        /* ——————————————————————————————————confirm模态框—————————————————————————————————— */
        confirmRes: '',
        myZhusu: [],
        cellmyZhusu: {}
      }
    },
    created () {
      this.getData()
    },
    methods: {
      /* 删除 */
      deleteSandian (id) {
        if (this.confirmRes === ''){
          this.confirmModal('真的要删除吗？')
        } else if (this.confirmRes === 'queren') {
          const self = this
          axios.post('deleteSRH/delSRH', {id: id, type: 'hotel'},{headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }).then(function (res) {
            console.log('删除成功' + res)
            window.location.reload()
          }).catch(function (err) {
            console.log('删除失败' + err)
            self.alertModal('删除失败')
            self.confirmRes = ''
          })
        } else if (this.confirmRes === 'quxiao') {
          this.confirmRes = ''
        }
      },
      /* alert模态框1 */
      closeBtn (parm) {
        if (this.showModalValue === 'modal-confirm') {
          if ( parm === 'quxiao') {
            this.confirmRes = 'quxiao'
            this.deleteSandian()
          } else {
            this.confirmRes = 'queren'
            this.deleteSandian()
          }
        }
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;'
        this.showModalValue = 'modal-alter'
      },
      /* confirm模态框2 */
      confirmModal (alertText) {
        this.noticeText = alertText
        this.showModalValue = 'modal-confirm'
      },
      getData () {
        const self = this
        axios.get('Hotel/allCountryHotels').then(function (res) {
          console.log('获取个人住宿成功！')
          self.myZhusu = []
          var data2_zs = res.data.hotels
          for (var datazsI = 0; datazsI < data2_zs.length; datazsI++) {
            self.cellmyZhusu = {}
            self.cellmyZhusu.zsName = data2_zs[datazsI].hotelname
            self.cellmyZhusu.zsImg = 'img/hotel/cover/' + data2_zs[datazsI].mainpic
            self.cellmyZhusu.zsId = data2_zs[datazsI].idhotel
            self.cellmyZhusu.zsTime = data2_zs[datazsI].addtime
            self.cellmyZhusu.location = data2_zs[datazsI].location
            self.myZhusu[datazsI] = self.cellmyZhusu
          }
        }).catch(function (err) {
          console.log('获取个人住宿失败：' + err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



