						document.getElementsByClassName('specialty2')[0].style.display = 'block'
					}, 10)
				}
			}
			this.displayNone(0)
    },
    ChangePlayShow (str) {
      this.play_show = str
			if (str === 'place') {
				if (this.jingdian.length > 0) {
					this.searchPleace(this.jingdian[0].name)
				} else {
					this.searchPleace('安吉县')
				}
			} else if (str === 'restaurant') {
				if (this.canguan.length > 0) {
					this.searchPleace(this.canguan[0].name)
				} else {
					this.searchPleace('安吉县')
				}
			} else if (str === 'hotel') {
				if (this.zhusu.length > 0) {
					this.searchPleace(this.zhusu[0].name)
				} else {
					this.searchPleace('安吉县')
				}
			}
