          this.alertModal('要登录后才能收藏哦~')
          return
        }
        else{
          const self = this
          this.sendScData.set('name', self.cName)
          this.sendScData.set('remark', 'wu')
          this.sendScData.set('mainPic', self.cCover)
          this.sendScData.set('id', self.GetQueryString('id'))
          console.log('收藏，向后台发送：\nname：' + this.sendScData.get('name') + '，remark：' + this.sendScData.get('remark') + '，mainPic：' + this.sendScData.get('mainPic')+ '，id：' + this.sendScData.get('id'))
