            self.alertModal('收藏失败~')
            console.log('收藏失败~' + err)
          })
        }
      }
      else {
        // 已收藏
        self.alertModal('您已经收藏过了~')
      }

    },
		/* 添加留言（前端的效果）（需要修改）（将其加入到 添加成功下面 并需要后台返回 当前用户的姓名） */
    addComment () {
      this.comment.splice(0, 0, {
        name: this.message.userName,
        headp: this.message.userPic,
        time: getTime(),
        content: this.commentText,
        like_show: 'like',
