        dislike: 0,
        scores: this.starNum
      })
      this.commentText = ''
      this.starNum = 0
    },
		/* 发送新的留言 */
		sendComment () {
      if (this.commentText.replace(/^\s+|\s+$/gm,'') === '') {
        this.alertModal('评论不能为空~')
        return
      }
      else if (this.message.userName === ''){
        this.alertModal('要登录后才能评论哦~')
        return
      }
			this.cellCommentAdd.content = this.commentText
			this.cellCommentAdd.scores = this.starNum
