			console.log('评分：' + this.cellCommentAdd.scores)
			console.log('村号：' + this.cellCommentAdd.id)
			axios.post('CountryComment/newComment', self.cellCommentAdd, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
