    btnDown () {
      if (this.techanInfo.length <= 3) {
        return
      } else {
        if (this.nowLocation === 121 * (this.techanInfo.length - 3)) {
          this.nowLocation = 0
        } else {
          this.nowLocation += 121
        }
        this.style2 = this.move
      }
    },
    btnUp () {
      if (this.nowLocation === 0) {
        this.nowLocation = 121 * (this.techanInfo.length - 3)
      } else {
        this.nowLocation -= 121
      }
      this.style2 = this.move
    },
    showChange (obj, index) {
      if (obj === 'modal-img') {
        this.showModalValue = 'modal-img'
        this.imgInfo = this.imgsInfo[index]
      } else if (obj === 'modal-text') {
        this.showModalValue = 'modal-text'
        this.showActIndex = index
      } else if (obj === 'modal-video') {
        this.showModalValue = 'modal-video'
        this.showVidIndex = index
      } else {
        this.showModalValue = ''
      }
    },
    showBtn () {
      this.showButtonValue = !this.showButtonValue
    },
    showBefore () {
      if (this.showImgIndex === 0) {
        this.showImgIndex = this.imgInfo.length - 1
      } else {
        this.showImgIndex -= 1
      }
    },
    showAfter () {
      if (this.showImgIndex === this.imgInfo.length - 1) {
        this.showImgIndex = 0
      } else {
        this.showImgIndex += 1
      }
    },
		/* 获取url中的参数 */
		GetQueryString (name) {
			var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
			var reg2 = new RegExp('([^=]+)$')
			var r = window.location.href.match(reg)
			if (r != null) {
				return r[0].match(reg2)[0]
			} else {
				return null
			}
		},
		/* 去除vue-html5-editor的样式 */
		displayNone (time) {
			setTimeout(
					function () {
						var toolbar = document.getElementsByClassName('toolbar')
						var editor = document.getElementsByClassName('vue-html5-editor')
						for (var i = 0; i < toolbar.length; i++) {
							toolbar[i].style.display = 'none'
						}
						for (var j = 0; j < editor.length; j++) {
							editor[j].style.border = '0px solid white'
						}
						console.log('执行了displayNone')
						var contentdiv = document.getElementsByClassName('content')
						for (var icontent = 0; icontent < contentdiv.length; icontent++) {
							contentdiv[icontent].contentEditable = 'false'
						}
					}, time
			)
		},
		/* 时间格式化 */
		dateFormat (time) {
			var date = new Date(time)
			var year = date.getFullYear()
			var month = date.getMonth() + 1
			var day = date.getDate()
			return year + '-' + month + '-' + day
		},
		menun () {
			window.scrollTo(0, 0)
		},
	},
	created () {
    if (this.GetQueryString('id') === '-1') {
      window.location.href = '#/404'
    }
    this.menun()
		this.displayNone(30)
		this.getContryside()
	},
  props: ['message']
}
function getTime () {
  var now = new Date()
  var year = now.getFullYear()
  var month = now.getMonth() + 1
  var day = now.getDate()
	month = parseInt(month) < 10 ? '0' + month : month
	day = parseInt(day) < 10 ? '0' + day : day
  return year + '-' + month + '-' + day
}
