		cursor: pointer;
	}
	.spe_right ul {
		width: 100%;
		height: 82%;
	}
	.spe_right li {
		width: 80%;
		height: 33%;
		margin: 0 auto;
	}
	.spe_right li img {
		width: 100%;
		height: 100%;
	}
/*文化*/
	.culture,
	.activity {
		width: 90.5%;
		min-height: 100px;
		margin: 0px auto 30px;
	}
	.culture ul li {
		line-height: 24px;
		text-indent: 34px;
		margin-bottom: 14px;
	}
/*活动*/
	.activity ul li {
		position: relative;
		cursor: pointer;
		line-height: 20px;
		margin-bottom: 14px;
	}
	.activity ul li span {
		position: absolute;
		right: 0;
		color: #989696;
	}
/*游玩*/
	.play {
		height: 500px;
		width: 91%;
		margin: 0 auto 30px;
	}
	.playkind {
		width: 7%;
		float: left;
	}
	.playkind ul {
		width: 100%;
	}
	.playkind ul li {
		width: 86%;
		height: 30px;
		line-height: 30px;
		background: #FFDE3A;
		color: #fff;
		text-align: center;
		margin-bottom: 40px;
		border-radius: 4px;
		cursor: pointer;
	}
	.place_chose {
		float: left;
		position: relative;
		height: 170px;
		width: 26px;
		overflow: hidden;
	}
	.place_chose_img1 {
		position: absolute;
		top: -190px;
		left: -4px;
	}
	.place_chose_img2 {
		position: absolute;
		top: -121px;
		left: -4px;
	}
	.place_chose_img3 {
		position: absolute;
		top: -50px;
		left: -4px;
	}
	.place_show {
		width: 250px;
		float: left;
	}
	.place_show ul {
		width: 100%;
	}
	.place_show ul li {
		width: 100%;
		height: 30px;
		line-height: 30px;
		cursor: pointer;
	}
	.place_show ul li img {
		margin: 0px 8px 0px 0px;
		font-size: 20px;
		vertical-align: middle;
	}
	.point_show {
		height: 450px;
		width: 650px;
		float: right;
		background: rgba(177, 211, 133, 0.8);
	}
/*游记*/
	.travel_notes {
		width: 100%;
		margin: 0 auto;
		cursor: pointer;
		min-height: 300px;
	}
	.one_tn {
		width: 22%;
		height: 260px;
		margin: 0px 0px 20px 2.2%;
		float: left;
	}
	.one_tn img {
		width: 100%;
		height: 78%;
	}
	.title_tn {
		width: 100%;
		height: 30px;
		line-height: 30px;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
		padding: 0px 5px;
		box-sizing: border-box;
	}
	.date_tn {
		width: 100%;
		font-size: small;
		color: #807e7e;
	}


/*标签*/
.biaoqain{
  width: 100%;
  margin: 0 auto;
  cursor: pointer;
  height: auto;
  padding-bottom: 30px;
}
.biaoqain ul{
  width: 100%;
  height: 50px;
  line-height: 50px;
  box-sizing: border-box;
  padding-left: 100px;
}
.biaoqain ul li{
  width: auto;
  padding: 0px 5px;
  float: left;
  height: 30px;
  line-height: 30px;
  border: 1px solid #cccccc;
  background: rgba(200,200,200,0.2);
  border-radius: 5px;
  margin-right: 15px;
}
.biaoqain ul .liAct{
  border: 1px solid #ffde3a;
  background: rgba(255,222,58,0.2);
  border-radius: 5px;
}
.user_touch_num{
  color: #999;
  font-size: small;
  display: inline;
}
.biaoqain ul .liAct .user_touch_num{
  color: #ff0000;
}
</style>



