<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active">我的景点</span>
    </div>
    <div class="midr2 content">
      <div v-if="myJingdian.length === 0">
        <router-link :to="{path: '/add/jingdianAdd', query:{type: 'sight'}}" target="_blank">
          <div class="divNone">
            <img src="static/moren.jpg" alt="">您还没有添加景点哟，快点击添加吧~
          </div>
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div v-if="myJingdian.length > 0">
        <router-link class="btn btn1" :to="{path: '/add/jingdianAdd', query:{type: 'sight'}}" target="_blank">
          添加景点
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myJd, index) in myJingdian">
        <div style="width: 300px;float: left;" class="imgcenter">
          <img :src="myJd.jdImg" style="height: 100%">
        </div>
        <div class="cellr">
          <router-link :to="{path: '/displayremark/sight', query: { id: myJd.jdId, type: 'sight' }}" target="_blank">
            <h4 v-text="myJd.jdName"></h4>
          </router-link>
          <p class="">{{myJd.location}}</p>
          <p class="" v-text="myJd.jdTime"></p>
          <p>
            <router-link class="btn btn-sm" :to="{path: '/add/jingdianAdd', query: { id: myJd.jdId, type: 'sight', type2: 'change' }}" target="_blank">
              修改
            </router-link>
            <a href="javascript:;" class="btn btn-sm btn-gray" @click="deleteSandian(myJd.jdId)" style="margin-left: 20px">
              删除
            </a>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
    <!-- confirm -->
    <div class="modal" v-if="showModalValue === 'modal-confirm'" style="z-index: 2000">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close" style="width: 50%;float: left;">
            <a href="javascript:;" class="link-btn" @click="closeBtn('quxiao')" style="color: #cccccc">取消</a>
          </button>
          <button class="alter-btn-close" style="width: 50%;float: left">
            <a href="javascript:;" class="link-btn" @click="closeBtn('queren')">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        /* ——————————————————————————————————confirm模态框—————————————————————————————————— */
        confirmRes: '',
        myJingdian: [],
        cellmyJingdian: {
          jdName: '',
          jdImg: '',
          jdId: 0,
          jdTime: '',
          location: ''
        }
      }
    },
    created () {
      this.getPersonJingdian()
    },
    methods: {
      /* 删除 */
      deleteSandian (id) {
        if (this.confirmRes === ''){
          this.confirmModal('真的要删除吗？')
        } else if (this.confirmRes === 'queren') {
          const self = this
          axios.post('deleteSRH/delSRH', {id: id, type: 'sight'},{headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }).then(function (res) {
            console.log('删除成功' + res)
            window.location.reload()
          }).catch(function (err) {
            console.log('删除失败' + err)
            self.alertModal('删除失败')
            self.confirmRes = ''
          })
        } else if (this.confirmRes === 'quxiao') {
          this.confirmRes = ''
        }
      },
      /* alert模态框1 */
      closeBtn (parm) {
        if (this.showModalValue === 'modal-confirm') {
          if ( parm === 'quxiao') {
            this.confirmRes = 'quxiao'
            this.deleteSandian()
          } else {
            this.confirmRes = 'queren'
            this.deleteSandian()
          }
        }
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;'
        this.showModalValue = 'modal-alter'
      },
      /* confirm模态框2 */
      confirmModal (alertText) {
        this.noticeText = alertText
        this.showModalValue = 'modal-confirm'
      },
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      },
      /* 获取 引dateFormat */
      getPersonJingdian () {
        const self = this
        axios.get('ScenicSpot/getSpots').then(function (res) {
          console.log('获取个人景点成功！')
          self.myJingdian = []
          var data2_jd = res.data.ScenicSpots
          for (var datajdI = 0; datajdI < data2_jd.length; datajdI++) {
            self.cellmyJingdian = {}
            self.cellmyJingdian.jdName = data2_jd[datajdI].name
            self.cellmyJingdian.jdImg = 'img/scienspot/cover/' + data2_jd[datajdI].cover
            self.cellmyJingdian.jdId = data2_jd[datajdI].scenicspotid
            self.cellmyJingdian.jdTime = data2_jd[datajdI].addtime
            self.cellmyJingdian.location = data2_jd[datajdI].location
            self.myJingdian[datajdI] = self.cellmyJingdian
          }
        }).catch(function (err) {
          console.log('获取个人景点失败：' + err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



