<template>
  <div class="personbar">
    <div class="topbar clear">
      <div class="top top1">
        <!-- <p>LOGO</p> -->
      </div>
      <div class="top top2">
        <img :src="userInfo2.userPic">
      </div>
      <div class="top top3">
        <div class="top3l">
          <br>
          <span v-text="userInfo2.userName" class="username"></span>
          <br>
          <!--<p>
            个性签名：
            <span v-text="userInfo2.signature"></span>
          </p>-->
        </div>
        <div class="top3r">
          <!--<p>
            等级：<span v-text="userInfo2.dengji"></span>
            经验：<span v-text="userInfo2.jingyan"></span>
            关注：<span v-text="userInfo2.guanzu"></span>
            粉丝：<span v-text="userInfo2.fensi"></span>
          </p>-->
        </div>
      </div>
    </div>
    <div class="midbar clear">
      <div class="midl">
        <div class="midl1 mid1"></div>
        <div class="midl2">
          <ul>
            <!--<li>
              <router-link :to="{ path: '/person/youji' }" :class="{active: show === 'youji'}" @click.native="showRouter('youji')">游记管理　></router-link>
            </li>-->
            <li>
              <router-link :to="{ path: '/person/xiangcun', query: {tab: 'xiangcun'} }" :class="{active: show === 'xiangcun'}" @click.native="showRouter('xiangcun')">乡村管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/jingdian', query: {tab: 'jingdian'} }" :class="{active: show === 'jingdian'}" @click.native="showRouter('jingdian')">景点管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/canguan', query: {tab: 'canguan'} }" :class="{active: show === 'canguan'}" @click.native="showRouter('canguan')">餐馆管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/zhusu', query: {tab: 'zhusu'} }" :class="{active: show === 'zhusu'}" @click.native="showRouter('zhusu')">住宿管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/gerenziliao', query: {tab: 'gerenziliao'} }" :class="{active: show === 'gerenziliao'}" @click.native="showRouter('gerenziliao')">个人资料　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/tongzhi', query: {tab: 'tongzhi'} }" :class="{active: show === 'tongzhi'}" @click.native="showRouter('tongzhi')">通　　知　></router-link>
            </li>
          </ul>
        </div>
        <div style="clear:both"></div>
      </div>
      <!-- 用户的基本信息，可传给子组件 :user-info="userInfo2 -->
      <router-view :user-info="userInfo2"></router-view>
    </div>
    <div class="footer clear">
      © 2017 ZheJiang. All Rights Reserved | Design by Us.
    </div>	
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'xiangcun',
        /* 用户的基本信息，可传给子组件 */
        userInfo2: {
          userName: '',
          signature: '',
          userPic: 'static/moren.jpg',
          dengji: 1,
          fensi: 2,
          guanzu: 3,
          jingyan: 4,
          sex: '男',
          birthday: '2017-01-01',
          city: '',
          phone: '',
          wechat: '',
          idcard: '',
          countrysideId: ''
        }
      }
    },
    created () {
      this.isLogin()
      this.show = this.GetQueryString('tab')
    },
    methods: {
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      showRouter (str) {
        this.show = str
        console.log(str)
      },
      getData () {
        const self = this
        axios.get('CountrysideUser/info').then(function (res) {
          console.log('成功获取info1数据：' + res)
          self.userInfo2.city = res.data.countrysideUser[0].city
          self.userInfo2.phone = res.data.countrysideUser[0].userphone
          self.userInfo2.sex = res.data.countrysideUser[0].sex
          self.userInfo2.userName = res.data.countrysideUser[0].name
          self.userInfo2.userPic = 'img/countrysideUser/headPic/' +  res.data.countrysideUser[0].headpic
          self.userInfo2.idcard = res.data.countrysideUser[0].idcard
          self.userInfo2.countrysideId = res.data.countrysideUser[0].countrysideId
          // self.userInfo2.birthday = res.data.countrysideUser.birth
          // self.userInfo2.signature = res.data.countrysideUser.signature
          // self.userInfo2.wechat = res.data.countrysideUser.wechat
        }).catch(function (err) {
          console.log('未能获取info1数据：' + err)
        })
      },
      isLogin () {
        const self = this
        axios.post('CountrysideUser/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
            }
        ).then(function (res) {
              if (res.data === '未登录') {
                console.log('商业用户没有登录')
                window.location.href = '/ssm01#/'
              } else {
                self.getData()
              }
            }
        ).catch(function (err) {
          console.log('商业用户判断404：' + err)
          window.location.href = '/ssm01#/'
        })
      }
    }
  }
</script>
<style scoped lang="stylus">
  @import "../../styles/center.css";
  @import "../../styles/style.css";

</style>



