      getOldData (id, url, dataname) {
        console.log('接收到的参数：' + id + ', ' + url + ', ' + dataname)
        const self = this
        axios.post(url, id, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
          }
        }).then(function (res) {
          console.log('接收到的信息如下：')
          var fData_jd = self.dataQianTai
          var aData_jd = res.data[dataname]
