    padding: 30px 30px;
    /*float: left;*/
    background: #e2dbdb;
    box-shadow: 0px 5px 15px 2px #666;
  }
  .main2{
    width: 200px;
    height: 400px;
    /*float: left;*/
    /*margin-left: 20px;*/
    background: #e2dbdb;
    box-shadow: 0px 5px 15px 2px #666;
  }
  .main2 ul{
    width: 150px;
    margin: 10px auto;
  }
  .main2 ul li{
    width: 100%;
    height: 30px;
  }
  .main2 ul li a{
    width: 100%;
    height: 30px;
    line-height: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    color: #666;
  }
  .main2 ul li a:hover{
    text-decoration: underline;
  }
  .main2 h3{
    width: 150px;
    margin: 10px auto 0px;
    color: #527b53;
  }
  .hengg1{
    height: 2px;
    width: 100%;
    margin: 0px auto 40px;
    background: #ccc;
  }
  /* 选择封面 */
  .add_cover{
    width: 66%;  
    height: 320px;
    background: #fff;
    border-radius: 3px;
    text-align: center;
    line-height: 320px;
    margin: 0px 0px 25px 120px; 
    color: #8a8a8a;
    border: 1px solid #ccc;
  }
  /*标题*/
  .title{
    width: 100%;
    height: 200px;
    line-height: 200px;
    text-align: center;
    font-size: 30px;
    letter-spacing: 10px;
    color: #446645;
    font-weight: bold;
  }
  /*各种信息填写*/
  input,button{
    font-family: '微软雅黑';
    font-size: 16px;
  }
  form{
    width: 100%;
  }
  .form_div{
    width: 100%;
    margin-bottom: 30px;
  }
  .form_div span{
    width: 20%;
    height: 45px;
    float: left;
    line-height: 45px;
    text-align: right;
  }
  .form_input{
    width: 50%;
    height: 45px;
    float: left;
    line-height: 45px;
    padding: 0px 10px;
    margin-left: 2%;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  .form_textarea{
    width: 50%;
    height: 200px;
    line-height: 30px;
    float: left;
    padding: 5px 10px;
    margin-left: 2%;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  .form_ul{
    width: 70%;
    height: 45px;
    float: left;
    margin-left: 2%;
  }
  .form_ul li{
    margin-right: 19px;
    height: 45px;
    float: left;
    line-height: 45px;
  }
  .form_select{
    height: 45px;
    line-height: 45px;
    width: 140px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  .form_cover_div{
    float: left;
    width: 70%;
    margin-left: 2%;
  }
  .form_cover_div img{
    width: 88%;
    height: 320px;
  }
  .cho_cov{
    width: 70%;
    height: 60px;
    margin-top: 20px;
  }
  .cho_cov button{
    width: 150px;
    height: 50px;
    background: #F2F2F2;
    border: 1px solid #F2F2F2;
    border-radius: 6px;
    float: left;
    text-align: center;
    line-height: 50px;
  }
  .cho_cov span{
    height: 50px;
    line-height: 50px;
    margin-left: 20px;
  }
  .form_pic_con,.form_video_con{
    width: 80%;
    float: left;
    min-height: 350px;
  }
  .form_video_div{
    width: 99%;
    height: 300px;
    margin-top: 5px;
    border-radius: 5px;
    border: 2px solid #F2F2F2;
  }

  /*提交等按钮*/
  .form_btn{
    width: 48.2%;
    float: right;
    height: 60px;
    margin: 30px 0px 40px;
  }
  .form_btn input{
    height: 40px;
    line-height: 40px;
    width: 90px;
    text-align: center;
    margin-right: 20px;
    background: #FFDE3A;
    border-radius: 3px;
    border: 1px solid #FFDE3A;
  }

  /*底部*/
  .footer{
    width: 100%;
    height: 120px;
    background: #000;
    color: #fff;
    text-align: center;
    line-height: 120px;
  }

  /* 照片集 */
  .files-div{
    width: auto;
    border-radius: 5px;
    border: 1px solid #ccc;
    min-height:295px;
    padding-left: 10px;
    padding-top: 10px;
    margin-top: 5px;
    background: #fff;
  }
  .img-cell{
    float: left; 
    width: 24%;
    margin: 10px 1% 0px 0px;
    height: 190px;
    position: relative;
  }
  .cho_pics{
    float: left; 
    width: 23.5%;
    margin: 10px 1% 0px 0px;
    height: 186px;
    border-radius: 5px;
    border: 2px solid #F2F2F2;
  }

</style>



