<template>
	<div class="main">
    <div class="centerinput clear">
      <img src="static/loadbg1.jpg">
      <div class="centerbox clear">
        <h2>&nbsp;&nbsp;&nbsp;</h2>
        <ul>
          <li><router-link :to="{ path: '/loadandreg/load' }" class="aFontColor">登录</router-link></li>
          <li><router-link :to="{ path: '/loadandreg/register' }" class="aFontColor">注册</router-link></li>
        </ul>
        <router-view></router-view>
      </div>
    </div>
	</div>
</template>
<script>
  export default {
  }
</script>
<style scoped>
@import '../../styles/main.css';
.main{
	background: url('/ssm01/static/bg.gif');
	box-sizing:border-box;
	width: 100%;
	height: 100%;
	padding: 20px 0px;
	border-top: 5px solid #ffde3a;
}
.aFontColor{
    color: #fff;
    text-decoration: none;
  }
.centerinput{
	width: 900px;
	height: 550px;
	position: relative;
	margin: 0px auto;
	box-shadow: 2px 2px 10px 2px #222;
}
.centerinput img{
	width: 100%;
	height: 100%;
}
.centerbox{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 0px;
	background: rgba(0,1,0,0.3);
}
h2{
	color: #FFDE3A;
	margin: 20px 30px;
}
.centerbox ul{
	width: 94%;
	height: 30px;
	margin: 0px 3%;
	/*background: rgba(111,222,55,0.8);*/
	border-bottom: 2px solid rgba(225,225,225,0.2);
}
.centerbox ul li{
	color: #fff;
	font-size: 18px;
	height: 30px;
	float: left;
	/*line-height: 30px;*/
	margin-left: 20px;
}
</style>



