<template>
  <div>
  	<router-view></router-view>
  	<div class="main_re clear">
	  	<div class="main_le clear">
			<!-- 评论 -->
				<div class="hengg1 clear"></div>
				<!-- 评分处 -->
				<div class="stars">
					<ul>
						<li>评分：</li>
						<li @click=" showStar (1) " v-if=" 1 <= starNum ">
							<img src="../../../static/icons/flo_s.png"></li>
						<li @click=" showStar (2) " v-if=" 2 <= starNum ">
							<img src="../../../static/icons/flo_s.png"></li>
						<li @click=" showStar (3) " v-if=" 3 <= starNum ">
							<img src="../../../static/icons/flo_s.png"></li>
						<li @click=" showStar (4) " v-if=" 4 <= starNum ">
							<img src="../../../static/icons/flo_s.png"></li>
						<li @click=" showStar (5) " v-if=" 5 <= starNum ">
							<img src="../../../static/icons/flo_s.png"></li>
						<li @click=" showStar (1) " v-if=" 1 > starNum ">
							<img src="../../../static/icons/flo_k.png"></li>
						<li @click=" showStar (2) " v-if=" 2 > starNum ">
							<img src="../../../static/icons/flo_k.png"></li>
						<li @click=" showStar (3) " v-if=" 3 > starNum ">
							<img src="../../../static/icons/flo_k.png"></li>
						<li @click=" showStar (4) " v-if=" 4 > starNum ">
							<img src="../../../static/icons/flo_k.png"></li>
						<li @click=" showStar (5) " v-if=" 5 > starNum ">
							<img src="../../../static/icons/flo_k.png"></li>
					</ul>
					<div><span>{{starNum}}.0</span>分</div>
				</div>
				<!-- 评论处 -->
				<form>
					<textarea class="remark_textarea" rows="13" placeholder="我有独特的见解..." v-model="commentText"></textarea>
					<input type="submit" class="clear remark_sub" value="提交" @click.prevent="sorry">
				</form>
				<!-- 用户的评论 -->
				<div class="re_notice" v-if="comment.length == 0">
					暂无评论，我来发表第一篇评论！
				</div>
				<div class="remark_con clear">
					<div class="one_rem clear"  v-for="(item,index) in comment" :index="index" >
						<div class="remark_infor">
							<div class="user_hp">
								<img :src="item.headp">
							</div>
							<div class="na_time">
								<div class="user_na">{{item.name}}</div>
								<div class="time_re">{{item.time}}</div>
							</div>
							<div class="like_dis">
								<ul>
									<li v-if="item.like_show === 'like'" @click="ChangeLike(item,'liked',1)"><img src="../../../static/icons/like.png"></li>
									<li v-if="item.like_show === 'liked'" @click="ChangeLike(item,'like',-1)"><img src="../../../static/icons/liked.png"></li>
									<li>{{item.like}}</li>
									<li v-if="item.dislike_show === 'dislike'" @click="ChangeDislike(item,'disliked',1)"><img src="../../../static/icons/dislike.png"></li>
									<li v-if="item.dislike_show === 'disliked'" @click="ChangeDislike(item,'dislike',-1)"><img src="../../../static/icons/disliked.png"></li>
									<li>{{item.dislike}}</li>
								</ul>
							</div>
						</div>
						<div class="remark_text clear">
							<div class="or_con">{{item.content}}</div>
							<div class="hengg2"></div>
							<div class="or_score clear">
								<ul>
									<li v-if=" 1 <= item.scores ">
										<img src="../../../static/icons/flo_s.png"></li>
									<li v-if=" 2 <= item.scores ">
										<img src="../../../static/icons/flo_s.png"></li>
									<li v-if=" 3 <= item.scores ">
										<img src="../../../static/icons/flo_s.png"></li>
									<li v-if=" 4 <= item.scores ">
										<img src="../../../static/icons/flo_s.png"></li>
									<li v-if=" 5 <= item.scores ">
										<img src="../../../static/icons/flo_s.png"></li>
									<li v-if=" 1 > item.scores ">
										<img src="../../../static/icons/flo_k.png"></li>
									<li v-if=" 2 > item.scores ">
										<img src="../../../static/icons/flo_k.png"></li>
									<li v-if=" 3 > item.scores ">
										<img src="../../../static/icons/flo_k.png"></li>
									<li v-if=" 4 > item.scores ">
										<img src="../../../static/icons/flo_k.png"></li>
									<li v-if=" 5 > item.scores ">
										<img src="../../../static/icons/flo_k.png"></li>
								</ul>
								<span style="color:#7b7b7b;">{{item.scores}}.0 分</span>
							</div>
						</div>
					</div>
				</div>
				<!-- 分页 -->
				<div class="pages" v-if="comment.length >= 5">
					<ul>
						<li style="color: #FFDE3A;border-color: #FFDE3A;">1</li>
						<li>2</li>
						<li>></li>
					</ul>
				</div>
	  	</div>
  	</div>
		<div class="footer">
			© 2017 ZheJiang. All Rights Reserved | Design by Us.
    </div>

    <!-- alert模态框 -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()">确认</a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        /* ——————————————————————————————————模态框—————————————————————————————————— */
        showModalValue: '',
        /* ——————————————————————————————————alert模态框—————————————————————————————————— */
        noticeText: '',
        aHref: 'javascript:;',
        starNum: 0,
        commentText: '',
        comment: []
      }
    },
    methods: {
      /* alert模态框1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert模态框2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
            this.showModalValue = 'modal-alter'
      },
      showStar (num) {
        this.starNum = num
      },
      addComment () {
        this.comment.push({
          name: '我',
          headp: 'static/hp1.jpg',
          time: getTime(),
          content: this.commentText,
          like_show: 'like',
          dislike_show: 'dislike',
          like: 0,
          dislike: 0,
          scores: this.starNum
        })
        this.commentText = ''
        this.starNum = 0
      },
      ChangeLike (item, str, n) {
        item.like_show = str
        item.like += n
      },
      ChangeDislike (item, str, n) {
        item.dislike_show = str
        item.dislike += n
      },
      /* 尚未开启的功能 */
      sorry () {
        this.alertModal('抱歉，该功能尚未开启')
      }
    }
  }
  function getTime () {
    var now = new Date()
    var year = now.getFullYear()
    var month = now.getMonth() + 1
    var day = now.getDate()
    month.length < 2 ? '0' + month : month
    day.length < 2 ? '0' + day : day
    return year + '-' + month + '-' + day
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/remark.css';
@import "../../styles/model.css";
.main_re{
	width: 1200px;
	margin: 0 auto;
}
.main_le{
	width: 70%;
	float: left;
}
.hengg1{
		height: 2px;
		width: 100%;
		margin: 0 auto;
		background: #ccc;
	}
.re_notice{
	width: 100%;
	height:100px;
	color: gray;
	text-align: center;
	margin-top: 100px;
}
</style>



