<script>
export default {
  data () {
    return {
      countrytype: '',
      location: '',
      cCover: '',
      cName: '',
      cIntroduction: '',
      cCulture: [],
      techanInfo: [],
      techanInfoSrc: [],
      techanInfoName: [],
      travelNotes: [],
      nowImgNum: 0,
      /* 点击的图片索引号码 */
      nowLocation: 0,
      /* 要上移动的高度 */
      style: '',
      /* 用 :class="style" 改变style的值 */
      style2: '',
      sight_show: 'all',
      special_show: 'culture',
      play_show: 'place',
      starNum: 0,
      commentText: '',
      comment: [],
      showModalValue: '',
      showButtonValue: false,
      showImgIndex: 0,    /* 当前显示的图片的索引号 */
      /* 图片数据 */
      imgsInfo: [],
      imgsInfoIndex: [],
      imgInfo: '',
      showVidIndex: 0,
      showActIndex: 0,
      actInfo: [],
      /* 视频数据 */
      videoInfo: [],
      shouedShow: false,
      shouTShow: false
    }
  },
  computed: {
    nowImg () {
      if (this.techanInfo.length === 0) {
        return null
      } else {
        return this.techanInfo[this.nowImgNum].src   /* 主图的索引号动态改变 */
      }
    },
    nowText () {
      if (this.techanInfo.length === 0) {
        return null
      } else {
        return this.techanInfo[this.nowImgNum].name    /* 主图下文字的索引号动态改变 */
      }
    },
    move () {
      return {
        transform: 'translateY(-' + this.nowLocation + 'px)',
        color: 'red',
        transition: 'all 0.5s'
      }
    },
    nowAct () {
      return this.actInfo[this.showActIndex]
    },
    /* 视频的数据 */
    nowVid () {
      return this.videoInfo[this.showVidIndex]
    }
  },
  methods: {
    ChangeSightShow (str) {
      this.sight_show = str
    },
    ChangeSpecialShow (str) {
      this.special_show = str
      if (str === 'specialty') {
        if (this.GetQueryString('images') === null) {
          setTimeout(function () {
            document.getElementsByClassName('specialty')[0].style.display = 'none'
            document.getElementsByClassName('specialty2')[0].style.display = 'block'
          }, 10)
        }
      }
      this.displayNone(0)
    },
    ChangePlayShow (str) {
      this.play_show = str
    },
    showStar (num) {
      this.starNum = num
    },
    showShowed (str) {
      this.shouedShow = str
    },
    addComment () {
      this.comment.push({
        name: '我',
        headp: 'static/hp1.jpg',
        time: getTime(),
        content: this.commentText,
        like_show: 'like',
        dislike_show: 'dislike',
        like: 0,
        dislike: 0,
        scores: this.starNum
      })
      this.commentText = ''
      this.starNum = 0
    },
    ChangeLike (item, str, n) {
      item.like_show = str
      item.like += n
    },
    ChangeDislike (item, str, n) {
      item.dislike_show = str
      item.dislike += n
    },
    changeImgIndex (index) {
      /* window.alert(index) */
      this.nowImgNum = index
    },
    btnDown () {
      /* window.alert(1) */
      /* this.style = 'move' */
      /* 用 :class="style" 改变style的值 */
      /* window.alert(this.aaa.length) */
      if (this.techanInfo.length <= 3) {
        return
      } else {
        if (this.nowLocation === 121 * (this.techanInfo.length - 3)) {
          this.nowLocation = 0
        } else {
          this.nowLocation += 121
        }
        this.style2 = this.move
      }
    },
    btnUp () {
      if (this.techanInfo.length <= 3) {
        return
      } else {
        if (this.nowLocation === 0) {
          this.nowLocation = 121 * (this.techanInfo.length - 3)
        } else {
          this.nowLocation -= 121
        }
        this.style2 = this.move
      }
    },
    showChange (obj, index) {
      if (obj === 'modal-img') {
        this.showModalValue = 'modal-img'
        this.imgInfo = this.imgsInfo[index]
      } else if (obj === 'modal-text') {
        this.showModalValue = 'modal-text'
        this.showActIndex = index
      } else if (obj === 'modal-video') {
        this.showModalValue = 'modal-video'
        this.showVidIndex = index
      } else {
        this.showModalValue = ''
      }
    },
    showBtn () {
      this.showButtonValue = !this.showButtonValue
    },
    showBefore () {
      if (this.showImgIndex === 0) {
        this.showImgIndex = this.imgInfo.length - 1
      } else {
        this.showImgIndex -= 1
      }
    },
    showAfter () {
      if (this.showImgIndex === this.imgInfo.length - 1) {
        this.showImgIndex = 0
      } else {
        this.showImgIndex += 1
      }
    },
    /* 获取url中的参数 */
    GetQueryString (name) {
      var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
      var reg2 = new RegExp('([^=]+)$')
      var r = window.location.href.match(reg)
      if (r != null) {
        return r[0].match(reg2)[0]
      } else {
        return null
      }
    },
    /* 格式化img的图片地址信息 */
    replaceFun1 (str, type) {
      if (str !== null) {
        return str.replace(/%3A/g, ':').replace(/%2F/g, '/')
      } else {
        if (type === 'null') {
          return ''
        } else if (type === 'array') {
          return []
        } else {
          return ''
        }
      }
    },
    /* 格式化array和json的字符串 */
    replaceFun3 (str, type) {
      if (str !== null) {
        return str.replace(/%3A/g, ':').replace(/%2F/g, '/').replace(/%3B/g, ';')
      } else {
        if (type === 'null') {
          return ''
        } else if (type === 'array') {
          return []
        } else {
          return ''
        }
      }
    },
    /* 格式化vue-html5-editor的内容 */
    replaceFun2 (str) {
      if (str !== null) {
        return str.replace(/%3D/g, '=').replace(/%3B/g, ';').replace(/%3A/g, ':').replace(/%2F/g, '/').replace(/%3C/, '<').replace(/%20/, ' ').replace(/%3D%22/g, '"').replace(/%20/g, '+')
      } else {
        return ''
      }
    },
    /* 去除vue-html5-editor的样式 */
    displayNone (time) {
      setTimeout(
        function () {
          var toolbar = document.getElementsByClassName('toolbar')
          var editor = document.getElementsByClassName('vue-html5-editor')
          for (var i = 0; i < toolbar.length; i++) {
            toolbar[i].style.display = 'none'
          }
          for (var j = 0; j < editor.length; j++) {
            editor[j].style.border = '0px solid white'
          }
          console.log('执行了displayNone')
          var contentdiv = document.getElementsByClassName('content')
          for (var icontent = 0; icontent < contentdiv.length; icontent++) {
            contentdiv[icontent].contentEditable = 'false'
          }
        }, time
      )
    }
  },
  created () {
    this.countrytype = decodeURI(this.GetQueryString('countrytype'))
    this.cName = decodeURI(this.GetQueryString('name'))
    this.cIntroduction = decodeURI(this.GetQueryString('introduction')).replace(/baifenhao/g,'%')
    this.location = decodeURI(this.GetQueryString('lishuxian'))
    this.cCover = decodeURI(this.replaceFun1(this.GetQueryString('fengmian'), 'null'))
    if (this.GetQueryString('techang') === null) {
      this.techanInfo = []
    } else {
      this.techanInfo = JSON.parse(decodeURI(this.replaceFun1(this.GetQueryString('techang'), 'array')))
    }
    console.log('---特产---')
    console.log(this.techanInfo)
    if (this.GetQueryString('images') === null) {
      this.imgsInfo = []
    } else {
      this.imgsInfo = []
      var imgsInfoI = decodeURI(this.replaceFun3(this.GetQueryString('images'), 'array')).split(';')
      console.log(imgsInfoI.length)
      for (var i = 0; i < imgsInfoI.length; i++) {
        var imgsIndoJ = imgsInfoI[i].split(',')
        this.imgsInfo[i] = imgsIndoJ
      }
    }
    console.log('---美景长度---' + this.imgsInfo.length)
    console.log(this.imgsInfo)
    if (this.GetQueryString('videos') === null) {
      this.videoInfo = []
    } else {
      this.videoInfo = []
      var videoInfoI = decodeURI(this.replaceFun1(this.GetQueryString('videos'), 'array')).split(',')
      this.videoInfo = videoInfoI
    }
    console.log('---视频长度---' + this.videoInfo.length)
    console.log(this.videoInfo)
    this.cCulture = decodeURI(this.replaceFun2(this.GetQueryString('wenhua')))
    this.actInfo = decodeURI(this.replaceFun2(this.GetQueryString('huodong')))
    console.log(this.GetQueryString('---文化---'))
    console.log('接受到的数据：{ 乡村名：' + this.cName + ',简介：' + this.cIntroduction + ',隶属：' + this.location + ',封面：' + this.cCover + ',特产：' + this.techanInfo + ',美景：' + this.imgsInfo + ',视频：' + this.videoInfo + ',文化：' + ',活动：' + this.actInfo + ' }')
    console.log(this.countrytype)
    this.displayNone(30)
  }
}
function getTime () {
  var now = new Date()
  var year = now.getFullYear()
  var month = now.getMonth() + 1
  var day = now.getDate()
  month = parseInt(month) < 10 ? '0' + month : month
  day = parseInt(day) < 10 ? '0' + day : day
  return year + '-' + month + '-' + day
}
</script>

