<template>
  <div>
		<div class="top_pic">
			<img src="static/finderror.jpg">
		</div>
		<div class="main">
		<!-- 标题 -->
			<div class="title">
				我要找错
			</div>
			<div class="hengg1"></div>
		<!-- 各种信息填写 -->
			<form class="clear">
				<div class="form_div clear">
					<span>问题类型：</span>
					<select class="form_select">
						<option>信息不符</option>
						<option>页面找不到</option>
						<option>错别字</option>
						<option>侵权</option>
						<option>盗版</option>
					</select>
				</div>
				<div class="form_div clear">
					<span style="line-height:30px;">上传图片：</span>
					<div class="xc_cover" style="margin-left: 220px">请选择图片证明</div>
					<span>&nbsp;</span>
					<a href="" class="a-file-btn-input" style="margin-left: 20px">
						<p>请选择图片</p>
						<input type="file" class="file-btn-input" id="" @change="fengmianFun('file-btn-input','xc_cover',0)">
					</a>
				</div>
				<div class="form_div clear">
					<span>问题页面网址：</span>
					<input type="text" class="form_input">
				</div>
				<div class="form_div clear">
					<span style="line-height:30px;">问题详述：</span>
					<textarea rows="13" class="form_textarea"></textarea>
				</div>
				<div class="form_div clear">
					<span>验证码：</span>
					<input type="text" class="identify_input">
					<div class="identify_show" style="width: 100px">
						<img alt="验证码" id="" :src="yanzhensrc"/>
					</div>
					<a @click="reloadCode" class="identify_change">看不清，换一张</a>
				</div>
				<div class="form_btn clear">
					<input type="reset" value="重置">
					<input type="submit" value="提交">
				</div>
			</form>


		</div>
  </div>
</template>
<script>
  export default {
		data () {
			return {
				yanzhensrc: 'servlet/ImageServlet',
			}
		},
		methods: {
			/* 封面图片 上传 */
			fengmianFun (class1, class2, index) {
				var choosePic = document.getElementsByClassName(class1)[index]
				var showPic = document.getElementsByClassName(class2)[index]
				showPic.innerHTML = ''
				var fileList = choosePic.files
				for (var i = 0; i < fileList.length; i++) {
					if (/^((?!image).)+/g.test(choosePic.files[i].type)) {
						this.alertModal('请选择正确的图片格式！')
						i = 99999
					} else {
						showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
						var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
						imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
						this.xcCover = imgObjPreview.src
						this.paramXc.set('mianpic', choosePic.files[0], choosePic.files[0].name)
					}
				}
			},
			reloadCode () {
				var time = new Date().getTime()
				this.yanzhensrc = 'servlet/ImageServlet?d=' + time
			},
		}
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import "../../styles/style.css";
.top_pic{
	height: 400px;
	width: 100%;
}
.top_pic img{
	width: 100%;
	height: 100%;
}

.main{
	/*height: 800px;*/
	width: 1000px;
	margin: 0px auto;
	/*background: rgba(55,220,188,0.7);*/
}
.hengg1{
	height: 2px;
	width: 100%;
	margin: 0px auto 40px;
	background: #ccc;
}
/*标题*/
	.title{
		width: 100%;
		height: 100px;
		line-height: 100px;
		text-align: center;
		font-size: 24px;
		letter-spacing: 10px;
	}
/*各种信息填写*/
	input,button{
		font-family: '微软雅黑';
		font-size: 16px;
	}
	form{
		width: 100%;
	}
	.form_div{
		width: 100%;
		margin-bottom: 30px;
	}
	.form_div span{
		width: 20%;
		height: 45px;
		float: left;
		line-height: 45px;
		text-align: right;
	}
	.form_select{
		height: 45px;
		line-height: 45px;
		width: 200px;
		margin-left: 2%;
		border-radius: 4px;
		border: 1px solid #ccc;
	}
	.form_div ul{
		width: 70%;
		/*height: 45px;*/
		float: left;
		margin-left: 2%;
	}
	.add_pic_ed{
		width: 33%;
	    height: 303px;
	    margin-right: 4%; 
	    float: left;
	}
	.add_pic_ed img{
		width: 100%;
		height: 250px;
	}
	.add_pic_ed button{
		width: 40%;
	    height: 35px;
	    text-align: center;
	    line-height: 29px;
	    margin: 10px 0px 0px 12px;
	    background: #F2F2F2;
		border: 1px solid #F2F2F2;
	}
	.add_pic_ing{
		width: 33%;
	    height: 303px;
	    margin-right: 4%; 
	    float: left;
		border-radius: 5px;
		border: 2px solid #F2F2F2;
	}
	.add_pic_ing button{
		width: 58%;
		height: 50px;
		text-align: center;
		line-height: 50px;
		margin: 123px 22% ;
		border-radius: 5px;
		background: #F2F2F2;
		border: 1px solid #F2F2F2;
	}
	.form_input{
		width: 63%;
		height: 45px;
		float: left;
		line-height: 45px;
		padding: 0px 10px; 
		margin-left: 2%;
		border-radius: 4px;
		border: 1px solid #ccc;
	}
	.form_textarea{
		width: 63%;
		height: 300px;
		line-height: 30px;
		float: left;
		padding: 5px 10px; 
		margin-left: 2%;
		border-radius: 4px;
		border: 1px solid #ccc;
	}
	.identify_input{
		width: 10%;
		float: left;
		margin-left: 2%;
		height: 45px;
		line-height: 45px;
		padding: 0px 10px; 
		border-radius: 4px;
		border: 1px solid #ccc;
	}
	.identify_show{
		width: 60px;
		height: 40px;
		float: left;
		background: rgba(177,22,221,0.7);
		margin-left: 3%;
	}
	.identify_show img{
		width: 100%;
		height: 100%;
	}
	.identify_change{
		height: 45px;
		float: left;
		line-height: 45px;
		margin-left: 3%;
		color: #bfbfbf;
		cursor: pointer;
	}


/*提交等按钮*/
	.form_btn{
		width: 48.2%;
		float: right;
		height: 60px;
		margin: 50px 0px 40px;
	}
	.form_btn input{
		height: 40px;
		line-height: 40px;
		width: 100px;
		text-align: center;
		margin-right: 70px;
		background: #FFDE3A;
		border-radius: 3px;
		border: 1px solid #FFDE3A;
	}
input{
	font-family: '微软雅黑';
	font-size: 16px;
}

</style>



